import axios from "axios";

import {
  ENV,
  LOCAL_BASE_URL,
  PROD_BASE_URL,
  DEV_BASE_URL,
} from "constants/app.constants";

let baseUrl = LOCAL_BASE_URL || "http://localhost:1337";

if (ENV === "prod") {
  baseUrl = PROD_BASE_URL;
}

if (ENV === "dev") {
  baseUrl = DEV_BASE_URL;
}

const axiosInstance = axios.create({
  baseURL: baseUrl,
  timeout: 10000,
});

export default axiosInstance;
