import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import cx from "classnames";
import { Container, Row, Col, InputGroup, FormControl } from "react-bootstrap";
import { Search } from "react-feather";

import styles from "./hero.module.css";
import { goto } from "./hero.actionHandlers";

const Hero = (props) => {
  const [searchText, setSearchText] = useState("");
  const [redirect, setRedirect] = useState(false);

  const handleSearch = (e) => {
    e.preventDefault();
    handleGoto();
    if (searchText.length !== 0) {
      return setRedirect(true);
    }
  };

  const handleChange = (value) => {
    handleGoto();
    setSearchText(value);
  };

  const handleGoto = () => {
    const { dispatch } = props;
    return dispatch(goto());
  };

  if (redirect)
    return (
      <Redirect
        to={{
          pathname: "/resource-listing",
          state: { searchText },
        }}
      />
    );

  return (
    <section
      className={cx({
        [styles.hero]: true,
        [styles.heroWithMinSearch]: props.withSearch,
      })}
    >
      <Container>
        <Row>
          <Col xs={12}>
            <div className={styles.content}>
              <h1>Global Coalition for COVID-19 Medical Care</h1>
              <ul className={styles.keywords}>
                <li>KNOWLEDGE</li>
                <li>ACCURACY</li>
                <li>EFFICIENCY</li>
              </ul>
            </div>
          </Col>
          <Col xs={12} className="d-none d-md-block">
            <form onSubmit={handleSearch}>
              <InputGroup
                className={cx("search-box mb-3", {
                  [styles.searchbox]: true,
                })}
              >
                <FormControl
                  className={styles.formcontrol}
                  placeholder="Search for Covid Resources: Articles, blogs, ..."
                  aria-label="Search"
                  onChange={(e) => handleChange(e.target.value)}
                  value={searchText}
                  onFocus={handleGoto}
                  aria-describedby="Search for Covid Resources: Articles, blogs, ..."
                />
                <InputGroup.Prepend className="input-group-prepend">
                  <InputGroup.Text
                    id="search"
                    style={{
                      backgroundColor: "white",
                      border: "none",
                      color: "#1ba6e1",
                      borderRadius: "4px",
                    }}
                  >
                    <Search
                      style={{ cursor: "pointer" }}
                      onClick={handleSearch}
                    />
                  </InputGroup.Text>
                </InputGroup.Prepend>
              </InputGroup>
            </form>
          </Col>
        </Row>
      </Container>
    </section>
  );
};

export default Hero;
