import Article from "services/apis/Article";
import UICONTROL_ACTIONS from "constants/redux-actions/uiControl.actions";

export const goto = (path, history) => (dispatch, getState) => {
  const { isAuthenticated } = getState().authControl;
  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  return history.push(path);
};

export const checkArticleIsSaved = ({ article }, state, setState) => async (
  _,
  getState
) => {
  const { isAuthenticated, userInfo, token } = getState().authControl;
  const { id } = article;
  if (!isAuthenticated) return;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.checkArticleIsSaved(id, data, token);
    return setState({ ...state, isSaved: true });
  } catch (error) {
    // console.log("check saved error", error);
    return setState({ ...state, isSaved: false });
  }
};

export const saveForLater = ({ article }, state, setState) => async (
  dispatch,
  getState
) => {
  const { isAuthenticated, userInfo, token } = getState().authControl;
  const { id } = article;

  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  if (state.isSaved) return;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.saveForLater(id, data, token);
    return setState({ ...state, isSaved: true });
  } catch (error) {
    // console.log("save for later error", error);
    return setState({ ...state, isSaved: false });
  }
};

export const unsaveArticle = ({ article }, state, setState) => async (
  dispatch,
  getState
) => {
  const { isAuthenticated, userInfo, token } = getState().authControl;
  const { id } = article;

  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  if (!state.isSaved) return;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.unsaveArticle(id, data, token);
    return setState({ ...state, isSaved: false });
  } catch (error) {
    // console.log("unsave article error", error);
    return setState({ ...state, isSaved: true });
  }
};
