import Content from "services/apis/Content";

import UICONTROL_ACTIONS from "constants/redux-actions/uiControl.actions";

import { ERROR } from "./homepage.constants";

export const goto = (path, history) => (dispatch, getState) => {
  const { isAuthenticated } = getState().authControl;
  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  if (path === null || path === undefined) return;
  return history.push(path);
};

export const onChangeHandler = ({ state, setState, value, event }) => () => {
  return setState({ ...state, [event.target.name]: value });
};

export const fetchHomepageContent = async (state, setState) => {
  try {
    setState({ ...state, loading: true });
    const response = await Content.home();
    // console.log("home", response);

    const { trending, recentlyAdded } = response.data;
    return setState({
      ...state,
      trending,
      recentlyAdded,
      loading: false,
    });
  } catch (error) {
    // console.log("content error", error);
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.FETCH_FAILED,
      loading: false,
    });
  }
};
