import React from "react";
import { Helmet } from "react-helmet";

import Container from "components/bits/Container";
import Header from "components/utilities/Header";
import SignUpForm from "components/utilities/SignUpForm";
import { TITLE_PREFIX } from "constants/app.constants";

function SignUp(props) {
  return (
    <React.Fragment>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Sign Up</title>
      </Helmet>

      <Container fluid style={{ padding: 0, marginBottom: 200 }}>
        <Header {...props} />
        <SignUpForm />
      </Container>
    </React.Fragment>
  );
}

export default SignUp;
