import { convertToRaw, EditorState } from "draft-js";
import { draftToMarkdown } from "markdown-draft-js";

import Article from "services/apis/Article";
import Upload from "services/apis/Upload";
import Category from "services/apis/Category";
import { EMPTY_STRING } from "constants/app.constants";

import { ERROR, HASHTAG_PATTERN, REGEX } from "./addArticle.constants";

export const fetchCategories = (state, setState) => async (_, getState) => {
  const { token } = getState().authControl;
  try {
    setState({ ...state, loading: true });
    const response = await Category.getAll(token);
    return setState({
      ...state,
      categories: response.data,
      loading: false,
    });
  } catch (error) {
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.FETCH_FAILED,
      loading: false,
    });
  }
};

export const addArticle = (state, setState) => async (_, getState) => {
  const { token, userInfo } = getState().authControl;

  const {
    category,
    title,
    content,
    image,
    externalLink,
    youtubeVideoLink,
    externalVideoLink,
    hashTags,
  } = state;

  const description = draftToMarkdown(
    convertToRaw(content.getCurrentContent())
  );

  if (
    category === EMPTY_STRING ||
    title === EMPTY_STRING ||
    description === EMPTY_STRING
  )
    return setState({ ...state, error: true, errorMessage: ERROR.EMPTY_FIELD });

  if (!image)
    return setState({ ...state, error: true, errorMessage: ERROR.EMPTY_IMAGE });

  if (hashTags !== EMPTY_STRING) {
    const tags = hashTags.split(" ");
    if (tags.length > 6) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.HASHTAG_LENGTH_EXCEEDED,
      });
    }

    const isValidHastag = tags.every((tag) => HASHTAG_PATTERN.test(tag));

    if (!isValidHastag) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.HASHTAG_NOT_VALID,
      });
    }
  }

  const slug = title
    .split(" ")
    .filter((word) => REGEX.test(word))
    .join("-");

  const values = {
    category: parseInt(category),
    type: "article",
    title,
    slug,
    description,
    externalLink,
    youtubeVideoLink,
    externalVideoLink,
    curatedBy: userInfo.id,
    author: userInfo.fullName,
    createdDate: new Date(),
    hashTags,
  };

  try {
    setState({ ...state, submitting: true, error: false, success: false });

    var formData = new FormData();
    formData.append("files", image);

    const fileUploadRes = await Upload.upload(formData, token);

    values.images = [fileUploadRes.data[0].id];
    await Article.add(values, token);

    return setState({
      ...state,
      submitting: false,
      category: EMPTY_STRING,
      title: EMPTY_STRING,
      hashTags: EMPTY_STRING,
      externalLink: EMPTY_STRING,
      youtubeVideoLink: EMPTY_STRING,
      externalVideoLink: EMPTY_STRING,
      content: EditorState.createEmpty(),
      image: null,
      error: false,
      success: true,
    });
  } catch (error) {
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.ARTICLE_ADD_FAILED,
      submitting: false,
      success: false,
    });
  }
};
