import React, { useState } from "react";
import { Link } from "react-router-dom";
import cx from "classnames";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";

import styles from "./forgotPasswordForm.module.css";
import ForgotPasswordOtpForm from "../ForgotPasswordOtpForm";
import Action from "./forgotPasswordForm.actionHandlers";
import { DEFAULT_STATE } from "./forgotPasswordForm.constants";

const ForgotPasswordForm = () => {
  const [state, setState] = useState(DEFAULT_STATE);

  const {
    email,
    sendingOtp,
    // error,
    // errorMessage,
    otpSent,
    otpData,
  } = state;

  const handleChange = (e) => {
    return setState({
      ...state,
      error: false,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    return Action.sendPasswordResetOtp(state, setState);
  };

  if (otpSent) {
    return <ForgotPasswordOtpForm otpData={otpData} />;
  }

  return (
    <>
      <Row style={{ marginRight: 0, marginLeft: 0 }}>
        <Col md={12}>
          <h3 className={styles.pageTitle}>Reset Password</h3>
        </Col>
      </Row>
      <Row
        className="justify-content-md-center"
        style={{ marginBottom: 50, marginRight: 0, marginLeft: 0 }}
      >
        <Col xs md={4}>
          <Form onSubmit={handleSubmit}>
            <Form.Group>
              <Form.Label htmlFor="formEmail" className={styles.label}>
                Email
              </Form.Label>
              <InputGroup className="mb-2">
                <FormControl
                  id="formEmail"
                  value={email}
                  type="text"
                  placeholder="Email"
                  name="email"
                  onChange={handleChange}
                />
              </InputGroup>
            </Form.Group>

            {state.error ? (
              <div className={styles.errorMessage}>{state.errorMessage}</div>
            ) : null}

            <Button
              variant="primary"
              type="submit"
              disabled={sendingOtp}
              className={cx("btn-block onboarding-btn", { [styles.btn]: true })}
              onClick={handleSubmit}
            >
              Send OTP
            </Button>
          </Form>
          <div className={styles.links}>
            <div>
              <Link to="/sign-in">Sign in</Link>
            </div>
          </div>
        </Col>
      </Row>
    </>
  );
};

export default ForgotPasswordForm;
