/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import cx from "classnames";
import moment from "moment";
import { AlignCenter } from "react-feather";
import { FiCloudOff } from "react-icons/fi";
import { Helmet } from "react-helmet";
import { BsBookmarkFill } from "react-icons/bs";
import { Breadcrumb } from "react-bootstrap";

import Container from "components/bits/Container";
import Spinner from "components/bits/Spinner";
import Row from "components/bits/Row";
import Col from "components/bits/Col";
import Navigation from "components/utilities/Navigation";
import Footer from "components/utilities/Footer";
import ThumbnailImage from "assets/images/default-image.png";
import {
  ENV,
  DEV_BASE_URL,
  PROD_BASE_URL,
  TITLE_PREFIX,
} from "constants/app.constants";

import styles from "./savedArticles.module.css";
import { getAuthInfo } from "./savedArticles.selector";
import {
  fetchSavedArticles,
  unsaveArticle,
} from "./savedArticles.actionHandlers";
import { DEFAULT_STATE } from "./savedArticles.constants";

const getImage = (images) => {
  if (images.length === 0) return ThumbnailImage;

  let baseUrl = DEV_BASE_URL;
  if (ENV === "prod") {
    baseUrl = PROD_BASE_URL;
  }

  const url = `${baseUrl}${images[0].url}`;

  return url;
};

const ResourceListing = React.memo((props) => {
  const [state, setState] = useState(DEFAULT_STATE);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(fetchSavedArticles(state, setState));
  }, []);

  const handleUnsave = (id, index) => {
    const { dispatch } = props;
    return dispatch(unsaveArticle(id, index, state, setState));
  };

  const renderBreadcrumb = () => (
    <Col xs={12} lg={6}>
      <Breadcrumb className={styles.bread}>
        <Breadcrumb.Item href="/" className={styles.breadcolor}>
          Home
        </Breadcrumb.Item>
        <Breadcrumb.Item className={styles.breadcolor} href="/resource-listing">
          Resources
        </Breadcrumb.Item>
        <Breadcrumb.Item active className={styles.breadcol}>
          Saved Articles
        </Breadcrumb.Item>
      </Breadcrumb>
    </Col>
  );

  const renderArticles = () => {
    const { savedArticles } = state;

    if (state.loading) {
      return (
        <div style={{ width: "100%", textAlign: "center", paddingTop: 200 }}>
          <Spinner animation="border" variant="info" />
          <div style={{ marginTop: 20 }}>Loading...</div>
        </div>
      );
    }

    if (savedArticles.length === 0) {
      return (
        <Col xs={12} style={{ textAlign: "center", marginTop: 50 }}>
          <div>
            <FiCloudOff fontSize={46} />
          </div>
          <div style={{ fontSize: 24, marginTop: 16 }}>No content found</div>
        </Col>
      );
    }

    return (
      <>
        {savedArticles.map(({ article }, index) => (
          <Col xs={12} lg={3} key={article.id}>
            {renderArticle(article, index)}
          </Col>
        ))}
      </>
    );
  };

  const renderArticle = (article, index) => (
    <div className={styles.articlecard}>
      <Row className="align-items-center">
        <Col xs={12}>
          <img
            className={cx("img-fluid", { [styles.imgfluid]: true })}
            src={getImage(article.images)}
            alt="img"
          />
          <div className={styles.articlecenter} href="#">
            <AlignCenter
              style={{ width: "16px", height: "16px", opacity: "0.5" }}
            />
            <span className={styles.art}>{article.type}</span>
          </div>

          <button
            className={styles.book}
            title="Unsave"
            onClick={() => handleUnsave(article.id, index)}
          >
            <BsBookmarkFill title="Unsave" style={{ fontSize: 21 }} />
          </button>
        </Col>

        <Col xs={12}>
          <span className={styles.taxo}>{article.category.name}</span>
          <a
            className={styles.title}
            title={article.title}
            rel="noopener noreferrer"
            target={
              !article.externalLink || article.externalLink.length === 0
                ? null
                : "_blank"
            }
            href={
              !article.externalLink || article.externalLink.length === 0
                ? `/resource-article/${article.slug}`
                : article.externalLink
            }
          >
            {article.title}
          </a>
          <div className={styles.footer}>
            <p className={styles.author} title={article.author}>
              By - {article.author}
            </p>
            <p
              className={cx("published right", {
                [styles.publishedright]: true,
              })}
            >
              {moment(article.created_at).format("MMM DD, YYYY")}
            </p>
          </div>
          <p className={styles.excerpt}>{`${article.description.slice(
            0,
            120
          )}...`}</p>
        </Col>
      </Row>
    </div>
  );

  return (
    <Container>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Saved Articles</title>
      </Helmet>

      <Navigation {...props} light />

      <Row style={{ marginTop: 90 }}>
        {renderBreadcrumb()}
        <Col xs={12}>
          <Container>
            <Row>{renderArticles()}</Row>
          </Container>
        </Col>
      </Row>
      <hr className={styles.horizontalLine} />
      <Footer />
    </Container>
  );
});

export default connect(getAuthInfo)(ResourceListing);
