import React from "react";
import { Route } from "react-router-dom";

const PrivateRoute = React.memo(
  ({ component: Component, isAuthenticated, userInfo, ...restProps }) => {
    return (
      <Route
        {...restProps}
        render={(props) => {
          if (isAuthenticated && userInfo.role.name === "Admin")
            return <Component {...props} />;

          return <div>You donot have access to view this page</div>;
        }}
      />
    );
  }
);

export default PrivateRoute;
