import Auth from "services/apis/Auth";
import { REGEX, EMPTY_STRING, OTP_RESEND_TIME } from "constants/app.constants";
import { _setErrorMsg } from "utils/common";

import { ERROR } from "./signUpOtpForm.constants";

export default {
  verifyOtp: async (otpData, state, setState) => {
    const { otp1, otp2, otp3, otp4, otp5, otp6 } = state;
    const { user, id } = otpData;

    if (
      otp1 === EMPTY_STRING ||
      otp2 === EMPTY_STRING ||
      otp3 === EMPTY_STRING ||
      otp4 === EMPTY_STRING ||
      otp5 === EMPTY_STRING ||
      otp6 === EMPTY_STRING
    )
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.EMPTY_FIELD,
      });

    if (
      !REGEX.SINGLE_DIGIT.test(otp1) ||
      !REGEX.SINGLE_DIGIT.test(otp2) ||
      !REGEX.SINGLE_DIGIT.test(otp3) ||
      !REGEX.SINGLE_DIGIT.test(otp4) ||
      !REGEX.SINGLE_DIGIT.test(otp5) ||
      !REGEX.SINGLE_DIGIT.test(otp6)
    ) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.INVALID_OTP,
      });
    }

    const otp = parseInt(`${otp1}${otp2}${otp3}${otp4}${otp5}${otp6}`);

    const values = {
      user,
      method: "register",
      otp,
      id,
    };

    try {
      setState({
        ...state,
        verifying: true,
      });
      const response = await Auth.verifyOtp(values);

      return setState({
        ...state,
        verifying: false,
        otpVerificationSuccess: true,
        showPopUp: true,
        fullName: response.data.user.fullName,
      });
    } catch (error) {
      // console.log("error", error);
      return setState({
        ...state,
        error: true,
        errorMessage: _setErrorMsg(
          error.response.data,
          ERROR.VEIRFICATION_FAILED
        ),
      });
    }
  },

  resendOtp: async (otpData, state, setState) => {
    const { user, id } = otpData;

    const values = {
      user,
      id,
    };

    setState({ ...state, resending: true });

    try {
      const response = await Auth.resendOtp(values);

      const newOtpData = { ...otpData, ...response.data };

      return setState({
        ...state,
        resendSuccess: true,
        resending: false,
        timer: OTP_RESEND_TIME,
        otpData: newOtpData,
        showResendBtn: false,
      });
    } catch (error) {
      // console.log("resend otp error", error);
      return setState({
        ...state,
        resending: false,
        error: true,
        errorMessage: _setErrorMsg(
          error.response.data,
          ERROR.OTP_RESEND_FAILED
        ),
      });
    }
  },
};
