import React, { useState } from "react";
import cx from "classnames";
import { Redirect } from "react-router-dom";
import Avatar from "react-avatar";
import { Search, Menu, ChevronDown } from "react-feather";

import image from "assets/images/GCCMCLogo.png";
import UICONTROL_ACTIONS from "constants/redux-actions/uiControl.actions";

import { signout } from "./navigation.actionHandlers";
import styles from "./navigation.module.css";

const Navigation = (props) => {
  const [state, setState] = useState({
    minSearchBar: false,
    miniMenu: false,
    actionMenu: false,
  });

  const [resourceType, setResourceType] = useState(null);
  const [redirect, setRedirect] = useState(false);

  const toggleMiniMenu = () =>
    setState({ ...state, minSearchBar: false, miniMenu: !state.miniMenu });

  const toggleMiniSearch = () => {
    const { dispatch } = props;
    return dispatch({ type: UICONTROL_ACTIONS.TOGGLE_MINI_SEARCHBAR });
  };

  const handleSignOut = () => {
    const { dispatch, history } = props;
    return dispatch(signout(history));
  };

  const handleSelectResourceType = (type) => {
    if (props.location.pathname === "/resource-listing") return;
    setResourceType(type);
    setRedirect(true);
  };

  const renderMenu = () => (
    <>
      <li>
        <div className={styles.dropdown}>
          <span>
            Resources
            <ChevronDown size={18} strokeWidth={3} />
          </span>
          <div className={styles.dropdowncontent}>
            <button
              className={styles.linkBtn}
              onClick={() => handleSelectResourceType("article")}
            >
              Articles
            </button>
            <button
              className={styles.linkBtn}
              onClick={() => handleSelectResourceType("video")}
            >
              Videos
            </button>
          </div>
        </div>
      </li>
      <li>
        <a href="/about-us">About Us</a>
      </li>
      <li>
        <a href="/contact-us">Contact Us</a>
      </li>
      <li className={cx({ [styles.miniMenuBtn]: state.miniMenu })}>
        {props.isAuthenticated ? (
          renderAvatar()
        ) : (
          <a href="/sign-in" className={styles.primaryBtn}>
            Sign In
          </a>
        )}
      </li>
    </>
  );

  const renderAvatar = () => (
    <div className={styles.actionMenuDropdown}>
      <span>
        <Avatar
          name={props.userInfo.fullName}
          round
          size="45"
          color="#DB6D6D"
          onClick={() => setState({ ...state, actionMenu: !state.actionMenu })}
        />
      </span>
      {state.actionMenu ? (
        <div
          className={cx({
            [styles.actionMenuDropdownContent]: true,
            [styles.actionMenu]: true,
          })}
        >
          <button
            className={styles.actionBtn}
            onClick={() => props.history.push("/saved-articles")}
          >
            Saved Articles
          </button>
          <button className={styles.actionBtn} onClick={handleSignOut}>
            Sign out
          </button>
        </div>
      ) : null}
    </div>
  );

  if (redirect)
    return (
      <Redirect
        to={{
          pathname: "/resource-listing",
          state: { resourceType },
        }}
      />
    );

  return (
    <nav
      className={cx({
        [styles.navbarContainer]: true,
        [styles.normalNav]: !props.light,
      })}
    >
      <div className={styles.navbar}>
        <div className={styles.main}>
          <div>
            <img
              className={styles.logo}
              alt="GCCMC_logo"
              src={image}
              onClick={() => props.history.push("/")}
            ></img>
          </div>
          <div className={styles.navLinks}>
            <ul>
              {renderMenu()}
              {props.location.pathname === "/" ? (
                <div className={styles.hamburgerBtnItem}>
                  <button
                    onClick={toggleMiniSearch}
                    className={styles.hamburgerBtn}
                  >
                    <Search size={28} />
                  </button>
                </div>
              ) : null}
              <div className={styles.hamburgerBtnItem}>
                <button
                  onClick={toggleMiniMenu}
                  className={styles.hamburgerBtn}
                >
                  <Menu size={28} />
                </button>
              </div>
            </ul>
          </div>
        </div>
      </div>

      {/* mini menu */}
      <div
        className={cx({
          [styles.miniMenu]: true,
          [styles.miniMenuOn]: state.miniMenu,
        })}
      >
        <ul>{renderMenu()}</ul>
      </div>
    </nav>
  );
};

export default Navigation;
