/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import cx from "classnames";
import { connect } from "react-redux";
import moment from "moment";
import { MdSwapVert } from "react-icons/md";
import { IoMdArrowDropdown } from "react-icons/io";
import { FiCloudOff } from "react-icons/fi";
import { Filter, Search } from "react-feather";
import { Helmet } from "react-helmet";
import {
  Dropdown,
  Breadcrumb,
  Form,
  InputGroup,
  FormControl,
} from "react-bootstrap";

import Container from "components/bits/Container";
import Spinner from "components/bits/Spinner";
import Row from "components/bits/Row";
import Col from "components/bits/Col";
import Navigation from "components/utilities/Navigation";
import Content from "components/utilities/Content";
import Footer from "components/utilities/Footer";
import {
  EMPTY_STRING,
  EMPTY_ARRAY,
  TITLE_PREFIX,
} from "constants/app.constants";

import styles from "./resourceListing.module.css";
import { getAuthInfo } from "./resourceListing.selector";
import {
  searchResources,
  fetchCategoriesAndSearch,
  handleCheckboxChange,
} from "./resourceListing.actionHandlers";
import {
  DEFAULT_STATE,
  INITIAL_LIMIT,
  INITIAL_OFFSET,
  sortOptions,
} from "./resourceListing.constants";

const ResourceListing = (props) => {
  const [state, setState] = useState({
    ...DEFAULT_STATE,
    searchText:
      props.location.state && props.location.state.searchText
        ? props.location.state.searchText
        : EMPTY_STRING,
    resourceTypes:
      props.location.state && props.location.state.resourceType
        ? [props.location.state.resourceType]
        : EMPTY_ARRAY,
  });

  const toggleArrow = () => setState({ ...state, arrowIcon: !state.arrowIcon });

  const clearAllfilters = () => {
    setState({
      ...state,
      limit: INITIAL_LIMIT,
      offset: INITIAL_OFFSET,
      selectedCategories: EMPTY_ARRAY,
      resourceTypes: EMPTY_ARRAY,
      searchText: EMPTY_STRING,
    });
  };

  const getActiveSortLabel = () => {
    const { sortBy } = state;
    const activeSrt = sortOptions.find((el) => el.value === sortBy);
    return activeSrt.text;
  };

  const handleAddFilter = (type, value, checked) => {
    return handleCheckboxChange(type, value, checked, state, setState);
  };

  const handleChange = (e) => {
    return setState({ ...state, searchText: e.target.value });
  };

  const handleSortByChange = (sortBy) => {
    return setState({ ...state, sortBy, showSort: false, arrowIcon: false });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { dispatch } = props;
    dispatch(searchResources(state, setState));
  };

  useEffect(() => {
    const { dispatch } = props;
    dispatch(fetchCategoriesAndSearch(state, setState));
  }, []);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(searchResources(state, setState));
  }, [
    state.selectedCategories,
    state.resourceTypes,
    state.limit,
    state.offset,
    state.sortBy,
  ]);

  const renderSortMenu = () => (
    <Col className="d-none d-lg-block" lg={6}>
      <div className={styles.dropdown}>
        <button className={styles.sortButton} onClick={toggleArrow}>
          <div style={{ display: "flex", justifyContent: "flex-end" }}>
            <div>Sort by:</div>
            <div className={styles.activeSort}>{getActiveSortLabel()}</div>
            <div>
              <IoMdArrowDropdown />
            </div>
          </div>
        </button>
        {state.arrowIcon ? (
          <div className={styles.dropdowncontent}>
            <div className={styles.pointer}></div>
            <div style={{ padding: "0px 15px" }} className={styles.sortby}>
              <span>Sort by:</span>
            </div>
            {sortOptions.map((op) => (
              <React.Fragment key={op.value}>
                <button
                  type="button"
                  className={cx("btn", {
                    [styles.btns]: true,
                    [styles.activeSortOp]: state.sortBy === op.value,
                  })}
                  onClick={() => handleSortByChange(op.value)}
                >
                  {op.text}
                </button>
                <Dropdown.Divider />
              </React.Fragment>
            ))}
          </div>
        ) : null}
      </div>
    </Col>
  );

  const renderCategoriesMenu = () => (
    <div className="d-none d-lg-block">
      <button
        type="button"
        className={cx("d-none d-lg-block btn btn-link", {
          [styles.cancelBtn]: true,
        })}
        onClick={() => clearAllfilters()}
      >
        Clear All Filters
      </button>

      <Form.Group>
        <Form.Label className={styles.head}>Resource Type</Form.Label>
        {state.resourcesList.map((res) => (
          <Form.Check
            key={res}
            className={styles.checkbox}
            type="checkbox"
            disabled={state.loading}
            checked={state.resourceTypes.includes(res)}
            label={res}
            onChange={(e) => handleAddFilter("resource", res, e.target.checked)}
          />
        ))}
      </Form.Group>

      <Form.Group>
        <Form.Label className={styles.head}>Category</Form.Label>
        {state.categories.map((cat) => (
          <Form.Check
            key={cat.id}
            disabled={state.loading}
            className={styles.checkbox}
            type="checkbox"
            checked={state.selectedCategories.includes(cat.id)}
            label={cat.name}
            onChange={(e) => handleAddFilter("category", cat, e.target.checked)}
          />
        ))}
      </Form.Group>
    </div>
  );

  const renderArticles = () => {
    const { articles } = state;

    if (state.loading) {
      return (
        <div style={{ width: "100%", textAlign: "center", paddingTop: 200 }}>
          <Spinner animation="border" variant="info" />
          <div style={{ marginTop: 20 }}>Loading...</div>
        </div>
      );
    }

    if (articles.length === 0) {
      return (
        <Col xs={12} style={{ textAlign: "center", marginTop: 50 }}>
          <div>
            <FiCloudOff fontSize={46} />
          </div>
          <div style={{ fontSize: 24 }}>No content found</div>
        </Col>
      );
    }

    return (
      <>
        {articles.map((article) => (
          <Col xs={12} lg={4} key={article.id}>
            <Content
              id={article.id}
              slug={article.slug}
              resourceType={article.type}
              category={article.category.name}
              title={article.title}
              author={article.author}
              excerpt={article.description}
              images={article.images}
              relevance={article.relevance}
              source={article.source}
              publishedOn={moment(article.created_at).format("MMM DD, YYYY")}
              externalLink={article.externalLink}
              {...props}
            />
          </Col>
        ))}
      </>
    );
  };

  return (
    <Container>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Resources</title>
      </Helmet>

      <Navigation {...props} light />

      <Row style={{ marginTop: 90 }}>
        {/* breadcrumb */}
        <Col lg={{ span: 9, offset: 3 }}>
          <Row>
            <Col xs={12} lg={6}>
              <Breadcrumb className={styles.bread}>
                <Breadcrumb.Item href="/" className={styles.breadcolor}>
                  Home
                </Breadcrumb.Item>
                <Breadcrumb.Item
                  className={styles.breadcolor}
                  href="/resource-listing"
                >
                  Resources
                </Breadcrumb.Item>
                <Breadcrumb.Item active className={styles.breadcol}>
                  Articles
                </Breadcrumb.Item>
              </Breadcrumb>
            </Col>

            {renderSortMenu()}
          </Row>
        </Col>

        {/* Menu */}
        <Col xs={12} lg={3} style={{ padding: "0px 26px" }}>
          <div className={styles.stickyFilters}>
            <Row>
              <Col xs={8} md={10} lg={12}>
                <form onSubmit={handleSubmit}>
                  <InputGroup
                    className={cx("search-box mb-3", {
                      [styles.searchbox]: true,
                    })}
                  >
                    <FormControl
                      placeholder="Search Resources"
                      aria-label="Search"
                      aria-describedby="Search Resources"
                      onChange={handleChange}
                      value={state.searchText}
                      name="searchText"
                      className={styles.formcontrol}
                    />
                    <InputGroup.Prepend className="input-group-prepend">
                      <InputGroup.Text
                        id="search"
                        className={styles.searchIcon}
                      >
                        <Search
                          size={14}
                          strokeWidth={3}
                          onClick={handleSubmit}
                          style={{ cursor: "pointer" }}
                          color="#9A9A9A"
                        />
                      </InputGroup.Text>
                    </InputGroup.Prepend>
                  </InputGroup>
                </form>
              </Col>
              <Col
                xs={2}
                md={1}
                className={cx("d-block d-lg-none", {
                  [styles.showSort]: true,
                })}
                style={{ paddingLeft: 0 }}
              >
                <button
                  className={cx({
                    [styles.linkbtn]: true,
                    [styles.btnOutlined]: state.showSort,
                  })}
                  onClick={() => {
                    return setState({
                      ...state,
                      showSort: !state.showSort,
                      categoryMenu: false,
                    });
                  }}
                >
                  <MdSwapVert
                    style={{ fontSize: 22, fontWeight: 600, letterSpacing: 2 }}
                    className={cx({
                      [styles.icons]: true,
                      [styles.iconOutlined]: state.showSort,
                    })}
                  />
                </button>
              </Col>
              <Col
                xs={2}
                md={1}
                className={cx("d-block d-lg-none", {
                  [styles.categoryMenuBtn]: true,
                })}
                style={{ paddingLeft: 0 }}
              >
                <button
                  className={cx({
                    [styles.linkbtn]: true,
                    [styles.btnOutlined]: state.categoryMenu,
                  })}
                  onClick={() => {
                    return setState({
                      ...state,
                      categoryMenu: !state.categoryMenu,
                      showSort: false,
                    });
                  }}
                >
                  <Filter
                    size={20}
                    strokeWidth={2}
                    className={cx({
                      [styles.icons]: true,
                      [styles.iconOutlined]: state.categoryMenu,
                    })}
                  />
                </button>
              </Col>
            </Row>

            {state.showSort ? (
              <div className={styles.sortMiniDropdowncontent}>
                <div className={styles.miniSortMenuList}>
                  <div className={styles.miniPointer}></div>
                  <div
                    style={{ padding: "0px 15px", marginTop: "10px" }}
                    className={styles.sortby}
                  >
                    <span>Sort by:</span>
                  </div>

                  {sortOptions.map((op) => (
                    <React.Fragment key={op.value}>
                      <button
                        type="button"
                        className={cx("btn", {
                          [styles.btns]: true,
                          [styles.activeSortOp]: state.sortBy === op.value,
                        })}
                        onClick={() => handleSortByChange(op.value)}
                      >
                        {op.text}
                      </button>
                      <Dropdown.Divider />
                    </React.Fragment>
                  ))}
                </div>
              </div>
            ) : null}

            {state.categoryMenu ? (
              <div className={styles.sortMiniDropdowncontent}>
                <div
                  className={styles.miniSortMenuList}
                  style={{ padding: "1rem" }}
                >
                  <div className={styles.miniPointerFilter}></div>
                  {renderCategoriesMenu()}
                </div>
              </div>
            ) : null}

            {renderCategoriesMenu()}
          </div>
        </Col>

        {/* main content/articles */}
        <Col xs={12} lg={9}>
          <Container className={styles.cont}>
            <Row>{renderArticles()}</Row>
          </Container>
        </Col>
      </Row>
      <hr className={styles.horizontalLine} />
      <Footer />
    </Container>
  );
};

export default connect(getAuthInfo)(ResourceListing);
