import Article from "services/apis/Article";

import UICONTROL_ACTIONS from "constants/redux-actions/uiControl.actions";

import { ERROR } from "./resourceArticle.constants";

export const fetchArticleDetails = (slug, state, setState) => async (
  _,
  getState
) => {
  const { token } = getState().authControl;

  const { limit, offset } = state;

  const params = {
    limit,
    offset,
  };

  try {
    setState({ ...state, loading: true });
    const response = await Article.findArticleBySlug(slug, params, token);
    const { article, relatedArticles } = response.data;

    return setState({
      ...state,
      article,
      relatedArticles,
      loading: false,
    });
  } catch (error) {
    // console.log("resource details error", error);
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.FETCH_FAILED,
      loading: false,
    });
  }
};

export const checkArticleIsSaved = ({ id }, state, setState) => async (
  _,
  getState
) => {
  const { isAuthenticated, userInfo, token } = getState().authControl;
  if (!isAuthenticated) return;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.checkArticleIsSaved(id, data, token);
    return setState({ ...state, isSaved: true });
  } catch (error) {
    console.log("check saved error", error);
    return setState({ ...state, isSaved: false });
  }
};

export const saveForLater = ({ id }, state, setState) => async (
  dispatch,
  getState
) => {
  const { isAuthenticated, userInfo, token } = getState().authControl;
  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  if (state.isSaved) return;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.saveForLater(id, data, token);
    return setState({ ...state, isSaved: true });
  } catch (error) {
    console.log("save for later error", error);
    return setState({ ...state, isSaved: false });
  }
};

export const vote = (id, state, setState) => async (dispatch, getState) => {
  const { isAuthenticated, token } = getState().authControl;

  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  try {
    const response = await Article.vote(id, token);
    return setState({ ...state, article: response.data.article });
  } catch (error) {
    // console.log("save for later error", error);
    return setState({ ...state });
  }
};

export const unsaveArticle = ({ id }, state, setState) => async (
  dispatch,
  getState
) => {
  const { isAuthenticated, userInfo, token } = getState().authControl;
  if (!isAuthenticated)
    return dispatch({ type: UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL });

  if (!state.isSaved) return;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.unsaveArticle(id, data, token);
    return setState({ ...state, isSaved: false });
  } catch (error) {
    console.log("unsave article error", error);
    return setState({ ...state, isSaved: true });
  }
};
