import React, { useState } from "react";
import cx from "classnames";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";

import SignInOtpForm from "../SignInOtpForm";
import styles from "./signInForm.module.css";
import Action from "./signInForm.actionHandlers";
import { DEFAULT_STATE } from "./signInForm.constants";
import { Link } from "react-router-dom";

const SignInForm = (props) => {
  const [state, setState] = useState(DEFAULT_STATE);
  const {
    identifier,
    password,
    authInProgress,
    error,
    errorMessage,
    otpSent,
    otpData,
  } = state;

  const handleChange = (e) => {
    return setState({
      ...state,
      error: false,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    return Action.executeSignIn(state, setState);
  };

  if (otpSent) {
    return <SignInOtpForm otpData={otpData} {...props} />;
  }

  return (
    <>
      <Row style={{ marginRight: 0, marginLeft: 0 }}>
        <Col xs={12}>
          <h3 className={styles.pageTitle}>Sign In</h3>
        </Col>
      </Row>
      <Row
        className="justify-content-md-center"
        style={{ marginBottom: 50, marginRight: 0, marginLeft: 0 }}
      >
        <Col xs md={4}>
          <Form onSubmit={handleSubmit}>
            <Form.Group>
              <Form.Label htmlFor="formEmail" className={styles.label}>
                Email
              </Form.Label>
              <InputGroup className="mb-2">
                <FormControl
                  id="formEmail"
                  value={identifier}
                  type="text"
                  placeholder="Email"
                  name="identifier"
                  onChange={handleChange}
                />
              </InputGroup>
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="password" className={styles.label}>
                Password
              </Form.Label>
              <InputGroup className="mb-2">
                <FormControl
                  type="password"
                  id="password"
                  placeholder="Password"
                  value={password}
                  name="password"
                  onChange={handleChange}
                />
              </InputGroup>
            </Form.Group>
            {error ? (
              <div className={styles.errorMessage}>{errorMessage}</div>
            ) : null}
            <Button
              variant="primary"
              type="submit"
              disabled={authInProgress}
              className={cx("btn-block onboarding-btn", { [styles.btn]: true })}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </Form>
          <div className={styles.links}>
            <div>
              <Link to="/forgot-password">Forgot Password?</Link>
            </div>
            <div>
              <Link to="/sign-up">Don't have an account? Sign Up</Link>
            </div>
          </div>
        </Col>
      </Row>
    </>
  );
};

export default SignInForm;
