import Auth from "services/apis/Auth";
import { REGEX, EMPTY_STRING } from "constants/app.constants";
import { _setErrorMsg } from "utils/common";

import { ERROR } from "./signInForm.constants";

export default {
  executeSignIn: async (state, setState) => {
    const { identifier, password } = state;

    if (identifier === EMPTY_STRING || password === EMPTY_STRING)
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.EMPTY_FIELD,
      });

    if (!REGEX.EMAIL.test(identifier))
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.INVALID_EMAIL,
      });

    const values = {
      identifier,
      password,
    };

    setState({ ...state, authInProgress: true });
    try {
      const response = await Auth.signIn(values);
      return setState({
        ...state,
        error: false,
        otpSent: true,
        otpData: response.data,
      });
    } catch (error) {
      // console.log("error", error.response);
      return setState({
        ...state,
        authInProgress: false,
        error: true,
        errorMessage: _setErrorMsg(error.response.data, ERROR.LOGIN_FAILED),
      });
    }
  },
};
