import SessionManager from "utils/SessionManager";
import { EMPTY_STRING } from "constants/app.constants";
import AUTHCONTROL_ACTIONS from "constants/redux-actions/authControl.actions";

export default {
  authenticationCheck: (loading, setLoading) => (dispatch) => {
    const token = SessionManager.getToken();
    if (!token || token === EMPTY_STRING) return setLoading(false);

    const decodedToken = SessionManager.decodeToken(token);

    // TODO decode the token and verify expiry date
    const payload = {
      token,
      userInfo: decodedToken,
    };

    dispatch({
      type: AUTHCONTROL_ACTIONS.AUTHENTICATION_SUCCESS,
      payload,
    });
    return setLoading(false);
  },
};
