import React, { useState } from "react";
import { Redirect } from "react-router-dom";
import cx from "classnames";
import Row from "react-bootstrap/Row";
import Col from "react-bootstrap/Col";
import Form from "react-bootstrap/Form";
import Button from "react-bootstrap/Button";
import InputGroup from "react-bootstrap/InputGroup";
import FormControl from "react-bootstrap/FormControl";

import styles from "./resetPasswordForm.module.css";
import Action from "./resetPasswordForm.actionHandlers";
import { DEFAULT_STATE } from "./resetPasswordForm.constants";

const ResetPasswordForm = ({ otpData }) => {
  const [state, setState] = useState(DEFAULT_STATE);

  const {
    password,
    confirmPassword,
    resettingPassword,
    resetPasswordSuccess,
    error,
    errorMessage,
  } = state;

  const handleChange = (e) => {
    return setState({
      ...state,
      error: false,
      [e.target.name]: e.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    return Action.resetPassword(otpData, state, setState);
  };

  if (resetPasswordSuccess) {
    return <Redirect to="/sign-in" />;
  }

  return (
    <>
      <Row style={{ marginRight: 0, marginLeft: 0 }}>
        <Col md={12}>
          <h3 className={styles.pageTitle}>Reset Password</h3>
        </Col>
      </Row>
      <Row
        className="justify-content-md-center"
        style={{ marginRight: 0, marginLeft: 0 }}
      >
        <Col xs md={4}>
          <Form onSubmit={handleSubmit}>
            <Form.Group>
              <Form.Label htmlFor="password" className={styles.label}>
                New Password
              </Form.Label>
              <InputGroup className="mb-2">
                <FormControl
                  id="password"
                  value={password}
                  type="password"
                  placeholder="New Password"
                  name="password"
                  onChange={handleChange}
                />
              </InputGroup>
            </Form.Group>
            <Form.Group>
              <Form.Label htmlFor="confirmPassword" className={styles.label}>
                Confirm Password
              </Form.Label>
              <InputGroup className="mb-2">
                <FormControl
                  type="password"
                  id="confirmPassword"
                  placeholder="Confirm Password"
                  value={confirmPassword}
                  name="confirmPassword"
                  onChange={handleChange}
                />
              </InputGroup>
            </Form.Group>

            {error ? (
              <div className={styles.errorMessage}>{errorMessage}</div>
            ) : null}

            <Button
              variant="primary"
              type="submit"
              disabled={resettingPassword}
              className={cx("btn-block onboarding-btn", { [styles.btn]: true })}
              onClick={handleSubmit}
            >
              Submit
            </Button>
          </Form>
        </Col>
      </Row>
    </>
  );
};

export default ResetPasswordForm;
