import React, { useState, useEffect, useRef } from "react";
import { Redirect } from "react-router-dom";
import cx from "classnames";
import { Button, Modal } from "react-bootstrap";

import Container from "components/bits/Container";
import Row from "components/bits/Row";
import Col from "components/bits/Col";
import { _countDownTimer } from "utils/common";

import styles from "./signUpOtpForm.module.css";
import Action from "./signUpOtpForm.actionHandlers";
import { DEFAULT_STATE } from "./signUpOtpForm.constants";

const MAX_REF_INDEX = 6;

const SignUpOtpForm = ({ otpData }) => {
  const [state, setState] = useState({ ...DEFAULT_STATE, otpData });

  const inputRefs = useRef([]);

  useEffect(() => {
    inputRefs.current = new Array(MAX_REF_INDEX);
  }, []);

  useEffect(() => {
    const interval = setInterval(() => {
      if (state.timer > 0) setState((s) => ({ ...s, timer: state.timer - 1 }));
    }, 1000);

    return () => {
      clearInterval(interval);
    };
  }, [state.timer]);

  useEffect(() => {
    if (state.timer === 0) {
      setState((s) => ({ ...s, showResendBtn: true }));
    }
  }, [state.timer]);

  // const handleClose = () => setState({ ...state, showPopUp: false });

  // const handleShow = () => setState({ ...state, show: true });

  const handleSubmit = (e) => {
    e.preventDefault();
    return Action.verifyOtp(state.otpData, state, setState);
  };

  const handleChange = (e) => {
    setState({
      ...state,
      [e.target.name]: e.target.value,
    });
    const refIndex = parseInt(e.target.name[3]);
    if (refIndex === MAX_REF_INDEX) return;
    return inputRefs.current[refIndex].focus();
  };

  const handleResendOtp = (e) => {
    e.preventDefault();
    return Action.resendOtp(state.otpData, state, setState);
  };

  if (state.redirect) {
    return <Redirect to="/" />;
  }

  return (
    <Container>
      <Row>
        <Col md={12}>
          <h3
            className={cx("onboarding-page_title", {
              [styles.title]: true,
            })}
          >
            Please Enter OTP
          </h3>
          <p
            className={cx("onboarding-page_sub_title", {
              [styles.tit]: true,
            })}
          >
            Your OTP has been sent to <b>{otpData.phoneNumber}</b>{" "}
            {/* <span className={styles.span} onClick={handleShow}>
              Edit
            </span>{" "} */}
          </p>
        </Col>
      </Row>
      <Row className="justify-content-md-center">
        <Col xs={12} md={4}>
          <form onSubmit={handleSubmit}>
            <Row>
              <Col xs={12}>
                <label className={styles.label}>Enter your OTP</label>
                <div className={styles.inputotp}>
                  <div className={styles.inputwrapper}>
                    <input
                      className={styles.input}
                      type="text"
                      maxLength="1"
                      size="1"
                      name="otp1"
                      value={state.otp1}
                      min="0"
                      onChange={handleChange}
                      max="9"
                      pattern="[0-9]{1}"
                      autoFocus
                      ref={(el) => (inputRefs.current[0] = el)}
                    />
                  </div>
                  <div className={styles.inputwrapper}>
                    <input
                      className={styles.input}
                      type="text"
                      maxLength="1"
                      size="1"
                      name="otp2"
                      onChange={handleChange}
                      min="0"
                      value={state.otp2}
                      max="9"
                      pattern="[0-9]{1}"
                      ref={(el) => (inputRefs.current[1] = el)}
                    />
                  </div>
                  <div className={styles.inputwrapper}>
                    <input
                      className={styles.input}
                      type="text"
                      maxLength="1"
                      size="1"
                      min="0"
                      value={state.otp3}
                      onChange={handleChange}
                      name="otp3"
                      max="9"
                      pattern="[0-9]{1}"
                      ref={(el) => (inputRefs.current[2] = el)}
                    />
                  </div>
                  <div className={styles.inputwrapper}>
                    <input
                      className={styles.input}
                      type="text"
                      onChange={handleChange}
                      maxLength="1"
                      size="1"
                      value={state.otp4}
                      name="otp4"
                      min="0"
                      max="9"
                      pattern="[0-9]{1}"
                      ref={(el) => (inputRefs.current[3] = el)}
                    />
                  </div>
                  <div className={styles.inputwrapper}>
                    <input
                      className={styles.input}
                      type="text"
                      maxLength="1"
                      value={state.otp5}
                      size="1"
                      onChange={handleChange}
                      min="0"
                      name="otp5"
                      max="9"
                      pattern="[0-9]{1}"
                      ref={(el) => (inputRefs.current[4] = el)}
                    />
                  </div>
                  <div className={styles.inputwrapper}>
                    <input
                      className={styles.input}
                      type="text"
                      maxLength="1"
                      value={state.otp6}
                      size="1"
                      onChange={handleChange}
                      name="otp6"
                      min="0"
                      max="9"
                      pattern="[0-9]{1}"
                      ref={(el) => (inputRefs.current[5] = el)}
                    />
                  </div>
                </div>
                {state.error ? (
                  <div className={styles.errorMessage}>
                    {state.errorMessage}
                  </div>
                ) : null}
                <p className={styles.p}>
                  {state.showResendBtn ? (
                    <Button
                      variant="link"
                      onClick={handleResendOtp}
                      className={styles.resendBtn}
                      disabled={state.resending}
                    >
                      {state.resending ? "Resending OTP..." : "Resend"}
                    </Button>
                  ) : (
                    <span>Resend OTP in {_countDownTimer(state.timer)}</span>
                  )}
                </p>
              </Col>
              <Col xs={12}>
                <Button
                  variant="primary"
                  type="submit"
                  disabled={state.verifying}
                  className={cx("btn-block onboarding-btn", {
                    [styles.btn]: true,
                  })}
                  onClick={handleSubmit}
                >
                  Submit
                </Button>
              </Col>
            </Row>
          </form>
        </Col>
      </Row>
      <Row>
        <Modal
          show={state.showPopUp}
          size="lg"
          aria-labelledby="contained-modal-title-vcenter"
          centered
        >
          <Modal.Body>
            <p
              style={{
                padding: "36px 56px",
                backgroundColor: "#E3F2F8",
                textAlign: "center",
                fontSize: "16px",
              }}
            >
              Hi Dr. {state.fullName}, Thank you, Our team will send a
              confirmation SMS of your GCCMC membership, once your credentials
              are verified.
            </p>
            <Button
              onClick={() =>
                setState({ ...state, showPopUp: false, redirect: true })
              }
              style={{
                backgroundColor: "#1BA7E2",
                borderColor: "#1BA7E2",
                float: "right",
              }}
            >
              Okay
            </Button>
          </Modal.Body>
        </Modal>
        {/* <Modal
          show={state.show}
          onHide={handleClose}
          animation={false}
          style={{ marginTop: "10%" }}
        >
          <Modal.Body>
            <Row>
              <Col>
                <label>Mobile Number</label>
                <div className="input-group mb-3">
                  <div className="input-group-prepend">
                    <span
                      className="input-group-text"
                      style={{ backgroundColor: "transparent" }}
                    >
                      +91
                    </span>
                  </div>
                  <input
                    type="text"
                    className="form-control"
                    placeholder="Enter Mobile number"
                  />
                </div>
              </Col>
            </Row>
          </Modal.Body>
        </Modal> */}
      </Row>
    </Container>
  );
};

export default SignUpOtpForm;
