import Article from "services/apis/Article";

import { ERROR } from "./savedArticles.constants";

export const fetchSavedArticles = (state, setState) => async (_, getState) => {
  const { token, userInfo } = getState().authControl;

  const { limit, offset } = state;

  const params = {
    limit,
    offset,
  };

  try {
    setState({ ...state, loading: true });
    const response = await Article.fetchSavedArticles(
      userInfo.id,
      params,
      token
    );

    const { savedArticles } = response.data;
    return setState({
      ...state,
      savedArticles,
      loading: false,
    });
  } catch (error) {
    // console.log("saved articles error", error);
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.FETCH_FAILED,
      loading: false,
    });
  }
};

export const unsaveArticle = (id, index, state, setState) => async (
  dispatch,
  getState
) => {
  const { userInfo, token } = getState().authControl;
  const savedArticlesDraft = state.savedArticles;

  try {
    const data = {
      user: userInfo.id,
    };

    await Article.unsaveArticle(id, data, token);
    savedArticlesDraft.splice(index, 1);
    return setState({ ...state, savedArticles: savedArticlesDraft });
  } catch (error) {
    // console.log("unsave article error", error);
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.UNSAVE_FAILED,
    });
  }
};
