import Auth from "services/apis/Auth";
import { REGEX, EMPTY_STRING } from "constants/app.constants";
import { _setErrorMsg } from "utils/common";

import { ERROR } from "./forgotPasswordForm.constants";

export default {
  sendPasswordResetOtp: async (state, setState) => {
    const { email } = state;

    if (email === EMPTY_STRING)
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.EMPTY_FIELD,
      });

    if (!REGEX.EMAIL.test(email))
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.INVALID_EMAIL,
      });

    const values = {
      email,
    };

    setState({ ...state, sendingOtp: true });
    try {
      const response = await Auth.forgotPassword(values);
      return setState({
        ...state,
        error: false,
        otpSent: true,
        otpData: response.data,
      });
    } catch (error) {
      // console.log("forgot password error", error);
      return setState({
        ...state,
        authInProgress: false,
        error: true,
        errorMessage: _setErrorMsg(
          error.response.data,
          ERROR.SENDING_OTP_FAILED
        ),
      });
    }
  },
};
