export const _identity = (item) => item;
export const _noop = () => {};

export const _countDownTimer = (seconds = 20) => {
  let timeString = "";

  if (seconds < 10) {
    timeString = `00:0${seconds}`;
  } else {
    timeString = `00:${seconds}`;
  }

  return timeString;
};

export const _setErrorMsg = (errorResponseData, staticMessage) => {
  if (!errorResponseData || !errorResponseData.error) {
    return staticMessage;
  }
  return errorResponseData.error.message;
};

export const _debounce = (time, callback) => {
  setTimeout(callback(), time);
};
