import ContactUs from "services/apis/ContactUs";
import { REGEX, EMPTY_STRING } from "constants/app.constants";
import { _setErrorMsg } from "utils/common";

import { ERROR } from "./contactUs.constants";

export const sendContactUs = (state, setState) => async () => {
  const { firstName, lastName, email, mobileNumber, message } = state;

  if (
    firstName === EMPTY_STRING ||
    lastName === EMPTY_STRING ||
    email === EMPTY_STRING ||
    mobileNumber === EMPTY_STRING ||
    message === EMPTY_STRING
  )
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.EMPTY_FIELD,
    });

  const data = {
    firstName: firstName.trim(),
    lastName: lastName.trim(),
    email: email.trim(),
    mobileNumber: mobileNumber.trim(),
    message: message.trim(),
  };

  if (!REGEX.EMAIL.test(data.email))
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.INVALID_EMAIL,
    });

  if (!REGEX.PHONE_NUMBER.test(data.mobileNumber))
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.INVALID_MOBILE_NUMBER,
    });

  setState({ ...state, submitting: true });
  try {
    await ContactUs.send(data);
    return setState({
      ...state,
      error: false,
      submitting: false,
      isModalOn: true,
      firstName: EMPTY_STRING,
      lastName: EMPTY_STRING,
      email: EMPTY_STRING,
      mobileNumber: EMPTY_STRING,
      message: EMPTY_STRING,
    });
  } catch (error) {
    // console.log("error", error.response);
    return setState({
      ...state,
      submitting: false,
      error: true,
      errorMessage: _setErrorMsg(error.response.data, ERROR.REQUEST_FAILED),
    });
  }
};
