import React from "react";
import {
  Container,
  Row,
  Col,
  Button,
  Form,
  InputGroup,
  FormControl,
} from "react-bootstrap";
import { MapPin, PhoneCall, Mail } from "react-feather";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";

import Navigation from "components/utilities/Navigation";
import Footer from "components/utilities/Footer";
import FormMessage from "components/pieces/FormMessage";
import { TITLE_PREFIX } from "constants/app.constants";

import styles from "./contactUs.module.css";
import { getAuthInfo } from "./contactUs.selector";
import { sendContactUs } from "./contactUs.actionHandlers";
import { DEFAULT_STATE } from "./contactUs.constants";

const Contact = (props) => {
  const [state, setState] = React.useState(DEFAULT_STATE);

  const hideModal = () => {
    return setState({
      ...state,
      isModalOn: false,
    });
  };

  const handleSubmit = () => {
    const { dispatch } = props;
    return dispatch(sendContactUs(state, setState));
  };

  const handleChange = (e) => {
    setState({ ...state, [e.target.name]: e.target.value });
  };

  return (
    <>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX}| Contact Us</title>
      </Helmet>
      <Navigation {...props} light />
      <Container style={{ marginTop: 85 }}>
        <Row>
          <Col>
            <h2 className={styles.page_title}>Write to Us</h2>
          </Col>
          <Col xs={12}>
            <p className={styles.section_desc}>
              Thank you for your interest in GCCMC. Kindly drop us a message and
              our team will get back to you shortly.
            </p>
          </Col>
        </Row>
        <form onSubmit={handleSubmit}>
          <Row>
            <Form.Group as={Col} xs={12} md={6}>
              <Form.Label>First Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter First Name"
                value={state.firstName}
                name="firstName"
                onChange={handleChange}
              />
            </Form.Group>

            <Form.Group as={Col} xs={12} md={6}>
              <Form.Label>Last Name</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Last Name"
                value={state.lastName}
                name="lastName"
                onChange={handleChange}
              />
            </Form.Group>
          </Row>

          <Row>
            <Form.Group as={Col} xs={12} md={6}>
              <Form.Label>Email Address</Form.Label>
              <Form.Control
                type="text"
                placeholder="Enter Email Address"
                value={state.email}
                name="email"
                onChange={handleChange}
              />
            </Form.Group>

            <Form.Group as={Col} xs={12} md={6}>
              <Form.Label htmlFor="formMobile">Mobile Number</Form.Label>
              <InputGroup className="mb-2">
                <InputGroup.Prepend>
                  <InputGroup.Text>+91</InputGroup.Text>
                </InputGroup.Prepend>
                <FormControl
                  id="formMobile"
                  placeholder="Enter Mobile Number"
                  value={state.mobileNumber}
                  name="mobileNumber"
                  onChange={handleChange}
                />
              </InputGroup>
            </Form.Group>
          </Row>

          <Form.Group controlId="formGridAddress2">
            <Form.Label>Message</Form.Label>
            <Form.Control
              as="textarea"
              rows={6}
              placeholder="Type your message"
              value={state.message}
              name="message"
              onChange={handleChange}
            />
          </Form.Group>

          {state.error ? (
            <div className={styles.errorMessage}>{state.errorMessage}</div>
          ) : null}

          <Row>
            <Col xs={12} style={{ textAlign: "right" }}>
              <Button
                variant="primary"
                className={styles.button}
                onClick={handleSubmit}
              >
                Submit
              </Button>
            </Col>
          </Row>
        </form>
      </Container>
      <FormMessage show={state.isModalOn} onHide={hideModal} />

      <section className={styles.locations}>
        <Container>
          <Row>
            <Col xs={12}>
              <h2 className={styles.page_title}>Contact Us</h2>
            </Col>
            <Col md={5}>
              <ul className={styles.links}>
                <li>
                  <span>
                    <MapPin />
                  </span>
                  <span>
                    GCCMC, Wipro Limited.
                    <br />
                    Doddakannelli, Sarjapur Road,
                    <br />
                    Bengaluru-560 035.
                  </span>
                </li>
                <li>
                  <span>
                    <PhoneCall />
                  </span>
                  <span className={styles.phoneNumbers}>
                    <a href="tel:+918061426698">+91 80 61426698</a>
                    &nbsp;&nbsp;|&nbsp;&nbsp;
                    <a href="tel:+918046726000">+91 80 46726000</a>
                    <br />
                    <a href="tel:+918046726698">+91 80 46726698</a>
                    &nbsp;&nbsp;|&nbsp;&nbsp;
                    <a href="tel:+917795655973">+91 77 95655973</a>
                  </span>
                </li>
                <li>
                  <span>
                    <Mail />
                  </span>
                  <span>
                    <a href="mailto:info@gccmc.in">info@gccmc.in</a>
                  </span>
                </li>
              </ul>
            </Col>
          </Row>
        </Container>
      </section>
      <Footer />
    </>
  );
};

export default connect(getAuthInfo)(Contact);
