/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import PropTypes from "prop-types";
import { Route, Switch } from "react-router-dom";
import { connect } from "react-redux";

import AuthCheckModal from "components/pieces/AuthCheckModal";
import Homepage from "pages/Homepage";
import Spinner from "components/bits/Spinner";
import SignUp from "pages/SignUp";
import SignIn from "pages/SignIn";
import ForgotPassword from "pages/ForgotPassword";
import MyProfile from "pages/MyProfile";
import AddArticle from "pages/AddArticle";
import PageNotFound from "pages/PageNotFound";
import ResourceListing from "pages/ResourceListing";
import SavedArticles from "pages/SavedArticles";
import ResourceArticle from "pages/ResourceArticle";
import ContactUs from "pages/ContactUs";
import AboutUs from "pages/AboutUs";
import TermsAndConditions from "pages/TermsAndConditions";
import PrivacyPolicy from "pages/PrivacyPolicy";

import PrivateRoute from "./PrivateRoute";
import PrivateAdminRoute from "./PrivateAdminRoute";
import { getAuthInfo } from "./routes.selector";
import Action from "./routes.actionHandlers";

const Routes = React.memo((props) => {
  const [loading, setLoading] = useState(true);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(Action.authenticationCheck(loading, setLoading));
  }, []);

  if (loading) {
    return (
      <div style={{ width: "100%", textAlign: "center", paddingTop: 200 }}>
        <Spinner animation="border" variant="info" />
        <div style={{ marginTop: 20 }}>Loading...</div>
      </div>
    );
  }

  return (
    <main>
      <AuthCheckModal />
      <Switch>
        <Route exact path="/" component={Homepage} />
        <Route exact path="/sign-in" component={SignIn} />
        <Route exact path="/sign-up" component={SignUp} />
        <Route exact path="/contact-us" component={ContactUs} />
        <Route exact path="/about-us" component={AboutUs} />
        <Route exact path="/privacy-policy" component={PrivacyPolicy} />
        <Route
          exact
          path="/terms-and-conditions"
          component={TermsAndConditions}
        />
        <Route exact path="/forgot-password" component={ForgotPassword} />

        <PrivateRoute
          isAuthenticated={props.isAuthenticated}
          exact
          path="/resource-article/:articleSlug"
          component={ResourceArticle}
        />
        <PrivateRoute
          isAuthenticated={props.isAuthenticated}
          exact
          path="/resource-listing"
          component={ResourceListing}
        />
        <PrivateRoute
          isAuthenticated={props.isAuthenticated}
          exact
          path="/saved-articles"
          component={SavedArticles}
        />
        <PrivateRoute
          isAuthenticated={props.isAuthenticated}
          exact
          path="/my-profile"
          component={MyProfile}
        />
        <PrivateAdminRoute
          isAuthenticated={props.isAuthenticated}
          userInfo={props.userInfo}
          exact
          path="/add-article"
          component={AddArticle}
        />
        <Route component={PageNotFound} />
      </Switch>
    </main>
  );
});

Routes.propTypes = {
  isAuthenticated: PropTypes.bool.isRequired,
};

export default connect(getAuthInfo)(Routes);
