import axios from "utils/axios";

export default {
  checkArticleIsSaved: async (id, data, token) => {
    const url = `/articles/${id}/is-saved`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "post", data, headers });
  },

  saveForLater: async (id, data, token) => {
    const url = `/articles/${id}/save-for-later`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "post", data, headers });
  },

  unsaveArticle: async (id, data, token) => {
    const url = `/articles/${id}/unsave-article`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "put", data, headers });
  },

  add: async (data, token) => {
    const url = `/articles`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "post", data, headers });
  },

  search: async (data, token) => {
    const url = `/resources/search`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "post", data, headers });
  },

  fetchSavedArticles: async (userId, params, token) => {
    const url = `/users/${userId}/saved-articles`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "get", params, headers });
  },

  findArticleBySlug: async (slug, params, token) => {
    const url = `/article-by-slug/${slug}`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "get", params, headers });
  },

  vote: async (id, token) => {
    const url = `/articles/${id}/votes`;
    const headers = { Authorization: `Bearer ${token}` };

    return axios({ url, method: "put", headers });
  },
};
