/* eslint-disable react-hooks/exhaustive-deps */
import React, { useEffect, useState } from "react";
import cx from "classnames";
import moment from "moment";
import { Row, Col } from "react-bootstrap";
import { Bookmark, AlignCenter, BookOpen } from "react-feather";
import { BsBookmarkFill } from "react-icons/bs";

import ThumbnailImage from "assets/images/thumb-01.jpg";
import {
  ENV,
  DEV_BASE_URL,
  PROD_BASE_URL,
  LOCAL_BASE_URL,
} from "constants/app.constants";

import styles from "./contentresource.module.css";
import {
  checkArticleIsSaved,
  saveForLater,
  unsaveArticle,
} from "./contentResource.actionHandlers";

const getImage = (images) => {
  if (images.length === 0) return ThumbnailImage;

  let baseUrl = LOCAL_BASE_URL || "http://localhost:1337";

  if (ENV === "prod") {
    baseUrl = PROD_BASE_URL;
  }

  if (ENV === "dev") {
    baseUrl = DEV_BASE_URL;
  }

  const url = `${baseUrl}${images[0].url}`;

  return url;
};

const getPublishedonDate = (createdDate, createdAt) => {
  if (!createdDate) {
    return moment(createdAt).format("MMM DD, YYYY");
  }
  return moment(createdDate).format("MMM DD, YYYY");
};

const ContentResource = (props) => {
  const [state, setState] = useState({ isSaved: false });

  useEffect(() => {
    const { dispatch } = props;
    dispatch(checkArticleIsSaved(props, state, setState));
  }, []);

  const handleSaveForLater = () => {
    const { dispatch } = props;
    return dispatch(saveForLater(props, state, setState));
  };

  const handleUnsave = () => {
    const { dispatch } = props;
    return dispatch(unsaveArticle(props, state, setState));
  };

  const renderCover = () => {
    if (props.article.type === "video") {
      return (
        <Col xs={12} className={styles.videoContainer}>
          <iframe
            className={styles.videoFrame}
            title={props.article.title}
            src={props.article.youtubeVideoLink}
            frameBorder="0"
            allow="accelerometer; autoplay; clipboard-write; encrypted-media; gyroscope; picture-in-picture"
            allowFullScreen
          ></iframe>
          <div className={styles.articlecenter}>
            <AlignCenter
              style={{ width: "16px", height: "16px", opacity: "0.5" }}
            />
            <span className={styles.art}>Video</span>
          </div>

          {state.isSaved ? (
            <button className={styles.book} onClick={handleUnsave}>
              <BsBookmarkFill style={{ fontSize: 21 }} />
            </button>
          ) : (
            <button className={styles.book} onClick={handleSaveForLater}>
              <Bookmark />
            </button>
          )}
        </Col>
      );
    }

    return (
      <Col xs={12}>
        <img
          className={cx("img-fluid", { [styles.imgfluid]: true })}
          src={getImage(props.article.images)}
          alt="img"
        />
        <div className={styles.articlecenter}>
          <AlignCenter
            style={{ width: "16px", height: "16px", opacity: "0.5" }}
          />
          <span className={styles.art}>Article</span>
        </div>

        {state.isSaved ? (
          <button className={styles.book} onClick={handleUnsave}>
            <BsBookmarkFill style={{ fontSize: 21 }} />
          </button>
        ) : (
          <button className={styles.book} onClick={handleSaveForLater}>
            <Bookmark />
          </button>
        )}
      </Col>
    );
  };

  return (
    <div className={styles.articlecard}>
      <Row className="align-items-center">
        {renderCover()}

        <Col xs={12}>
          <span className={styles.taxo}>{props.article.category.name}</span>
          <span className={styles.title}>{props.article.title}</span>
          <hr />
          <div
            className={cx("bookmark left", { [styles.bookmark]: true })}
            style={{ display: "inline-block" }}
          >
            <BookOpen />
            <span style={{ display: "inline-block", marginLeft: 8 }}>
              No of Reads: <strong>{props.article.views}</strong>
            </span>
          </div>
          <div
            className={cx("bookmark left", { [styles.bookmark]: true })}
            style={{ display: "inline-block", marginLeft: 30 }}
          >
            <span style={{ display: "inline-block", marginLeft: 8 }}>
              By: {props.article.author}
            </span>
          </div>

          <div
            className={cx("bookmark left", { [styles.bookmark]: true })}
            style={{ display: "inline-block", marginLeft: 30 }}
          >
            <span style={{ display: "inline-block", marginLeft: 8 }}>
              <strong>.</strong>&nbsp;
              {getPublishedonDate(
                props.article.createdDate,
                props.article.created_at
              )}
            </span>
          </div>
          <hr />
          <p className={styles.excerpt}>{props.article.description}</p>
        </Col>
      </Row>
    </div>
  );
};

export default ContentResource;
