/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import moment from "moment";
import cx from "classnames";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { compose } from "redux";
import { Container, Row, Col, FormControl, InputGroup } from "react-bootstrap";
import { Helmet } from "react-helmet";
import { Search } from "react-feather";

import Navigation from "components/utilities/Navigation";
import Hero from "components/utilities/Hero";
import Trending from "components/utilities/Trending";
import Article from "components/utilities/Article";
import Footer from "components/utilities/Footer";
import { TITLE_PREFIX } from "constants/app.constants";

import { fetchHomepageContent } from "./homepage.actionHandlers";
import styles from "./homepage.module.css";
import { goto } from "./homepage.actionHandlers";
import { getAuthInfo, getUIInfo } from "./homepage.selector";
import { DEFAULT_STATE } from "./homepage.constants";

const getPublishedonDate = (createdDate, createdAt) => {
  if (!createdDate) {
    return moment(createdAt).format("MMM DD, YYYY");
  }
  return moment(createdDate).format("MMM DD, YYYY");
};

const Homepage = (props) => {
  const [state, setState] = useState(DEFAULT_STATE);
  const [searchText, setSearchText] = useState("");
  const [lightNav, setLightNav] = useState(false);
  const [redirect, setRedirect] = useState(false);

  const handleGoto = (path) => {
    const { dispatch, history } = props;
    return dispatch(goto(path, history));
  };

  const handleChange = (value) => {
    handleGoto();
    setSearchText(value);
  };

  const handleScroll = () => {
    if (window.scrollY > 180) return setLightNav(true);
    return setLightNav(false);
  };

  const handleSearch = (e) => {
    e.preventDefault();
    handleGoto();
    if (props.location.pathname === "/resource-listing") return;
    if (searchText.length !== 0) {
      return setRedirect(true);
    }
  };

  useEffect(() => {
    window.addEventListener("scroll", handleScroll);
    fetchHomepageContent(state, setState);
    return () => window.removeEventListener("scroll", handleScroll);
  }, []);

  const renderMiniSearchBar = () => {
    const { miniSearchBarOn } = props;

    if (!miniSearchBarOn) return;

    return (
      <div
        className={cx("d-block d-md-none", {
          [styles.miniSearch]: true,
        })}
      >
        <Row style={{ marginRight: 0, marginLeft: 0 }}>
          <Col xs={12}>
            <form onSubmit={handleSearch}>
              <InputGroup
                className={cx("search-box mb-3", { [styles.searchbox]: true })}
              >
                <FormControl
                  className={styles.formcontrol}
                  onChange={(e) => handleChange(e.target.value)}
                  value={searchText}
                  onFocus={() => handleGoto()}
                  placeholder="Search for Covid Resources: Articles, blogs..."
                  aria-label="Search"
                  aria-describedby="Search for Covid Resources: Articles, blogs..."
                />
                <InputGroup.Prepend className="input-group-prepend">
                  <InputGroup.Text id="search" className={styles.searchIcon}>
                    <Search
                      size={18}
                      style={{ cursor: "pointer" }}
                      onClick={handleSearch}
                    />
                  </InputGroup.Text>
                </InputGroup.Prepend>
              </InputGroup>
            </form>
          </Col>
        </Row>
      </div>
    );
  };

  if (redirect)
    return (
      <Redirect
        to={{
          pathname: "/resource-listing",
          state: { searchText },
        }}
      />
    );

  return (
    <div>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX}| Home</title>
      </Helmet>

      <Navigation {...props} light={lightNav} />
      {renderMiniSearchBar()}
      <Hero {...props} withSearch={props.miniSearchBarOn} />

      <Container>
        <Row>
          <Col xs={12}>
            <div className={styles.sectiontitle}>
              <h2 style={{ marginBottom: 0 }}>Trending</h2>
            </div>
            {state.trending.map((article) => (
              <Trending
                key={article.id}
                id={article.id}
                slug={article.slug}
                images={article.images}
                externalLink={article.externalLink}
                source={article.source}
                relevance={article.relevance}
                category={article.category.name}
                title={article.title}
                author={article.author}
                excerpt={
                  article.description.length > 400
                    ? `${article.description.slice(0, 400)}...`
                    : article.description
                }
                publishedOn={getPublishedonDate(
                  article.createdDate,
                  article.created_at
                )}
                {...props}
              />
            ))}
          </Col>
        </Row>
        <Row className="d-block d-md-flex">
          <Col xs={12}>
            <div className={styles.sectiontitle}>
              <h2 style={{ marginBottom: 0 }}>Recently Added</h2>
              <button
                onClick={() => handleGoto(`/resource-listing`)}
                className={cx("d-none d-md-block", { [styles.link]: true })}
              >
                View All
              </button>
            </div>
          </Col>

          {state.recentlyAdded.map((article, index) => (
            <Col xs={12} md={6} key={article.id}>
              <Article
                id={article.id}
                slug={article.slug}
                category={article.category.name}
                title={article.title}
                source={article.source}
                relevance={article.relevance}
                externalLink={article.externalLink}
                author={article.author}
                excerpt={
                  article.description.length > 300
                    ? `${article.description.slice(0, 300)}...`
                    : article.description
                }
                publishedOn={getPublishedonDate(
                  article.createdDate,
                  article.created_at
                )}
                additionalClassName={cx({
                  [styles.borderBottomForSecondLast]:
                    index === state.recentlyAdded.length - 2,
                  [styles.borderBottom]:
                    index === state.recentlyAdded.length - 1,
                })}
                {...props}
              />
            </Col>
          ))}
          <Col className="d-block d-md-none text-center">
            <button
              onClick={() => handleGoto(`/resource-listing`)}
              className={styles.view_all}
            >
              View All
            </button>
          </Col>
        </Row>
        <hr className={styles.horizontalLine} />
      </Container>
      <Footer />
    </div>
  );
};

export default compose(connect(getUIInfo), connect(getAuthInfo))(Homepage);
