import React from "react";
import { connect } from "react-redux";
import { Helmet } from "react-helmet";

import Container from "components/bits/Container";
import Header from "components/utilities/Header";
import SignInForm from "components/utilities/SignInForm";
import { getAuthInfo } from "./signIn.selector";
import { TITLE_PREFIX } from "constants/app.constants";

const SignIn = (props) => {
  return (
    <div>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Sign In</title>
      </Helmet>

      <Container fluid style={{ padding: 0 }}>
        <Header {...props} />
        <SignInForm {...props} />
      </Container>
    </div>
  );
};

export default connect(getAuthInfo)(SignIn);
