import Article from "services/apis/Article";
import Category from "services/apis/Category";

import { ERROR } from "./resourceListing.constants";

export const handleCheckboxChange = (
  type = "resource",
  value,
  checked,
  state,
  setState
) => {
  const resourceTypesDraft = [...state.resourceTypes];
  const selectedCategoriesDraft = [...state.selectedCategories];

  let index;

  if (type === "category") {
    index = selectedCategoriesDraft.findIndex((cat) => cat === value.id);

    if (!checked) {
      if (index === -1) return;
      selectedCategoriesDraft.splice(index, 1);
    } else {
      if (index >= 0) return;
      selectedCategoriesDraft.push(value.id);
    }

    return setState({
      ...state,
      selectedCategories: selectedCategoriesDraft,
    });
  } else {
    index = resourceTypesDraft.findIndex((res) => res === value);
    if (!checked) {
      if (index === -1) return;
      resourceTypesDraft.splice(index, 1);
    } else {
      if (index >= 0) return;
      resourceTypesDraft.push(value);
    }
    return setState({ ...state, resourceTypes: resourceTypesDraft });
  }
};

export const fetchCategoriesAndSearch = (state, setState) => async (
  _,
  getState
) => {
  const { token } = getState().authControl;

  const {
    limit,
    offset,
    selectedCategories,
    resourceTypes,
    searchText,
    sortBy,
  } = state;

  const data = {
    limit,
    offset,
    searchText: searchText.trim(),
    resourceTypes,
    sortBy,
  };

  if (selectedCategories.length > 0) {
    data.categories = selectedCategories;
  }

  try {
    const categoryResponse = await Category.getAll(token);
    const categories = categoryResponse.data;

    const response = await Article.search(data, token);
    const { articles } = response.data;

    return setState({
      ...state,
      categories,
      articles,
      loading: false,
    });
  } catch (error) {
    // console.log("resource listing error", error);
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.FETCH_FAILED,
      loading: false,
    });
  }
};

export const searchResources = (state, setState) => async (_, getState) => {
  const { token } = getState().authControl;

  const {
    limit,
    offset,
    selectedCategories,
    resourceTypes,
    searchText,
    sortBy,
  } = state;

  const data = {
    limit,
    offset,
    searchText: searchText.trim(),
    resourceTypes,
    sortBy,
  };

  if (selectedCategories.length > 0) {
    data.categories = selectedCategories;
  }

  try {
    setState({ ...state, loading: true });
    const response = await Article.search(data, token);

    const { articles } = response.data;
    return setState({
      ...state,
      articles,
      loading: false,
    });
  } catch (error) {
    // console.log("resource listing error", error);
    return setState({
      ...state,
      error: true,
      errorMessage: ERROR.FETCH_FAILED,
      loading: false,
    });
  }
};
