/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import cx from "classnames";
import { Row, Col } from "react-bootstrap";
import { Bookmark, AlignCenter } from "react-feather";
import { BsBookmarkFill } from "react-icons/bs";

import ThumbnailImage from "assets/images/default-image.png";
import {
  ENV,
  DEV_BASE_URL,
  PROD_BASE_URL,
  LOCAL_BASE_URL,
  EMPTY_STRING,
} from "constants/app.constants";

import styles from "./content.module.css";
import {
  checkArticleIsSaved,
  saveForLater,
  unsaveArticle,
} from "./content.actionHandlers";
import { RELEVANCE_LIST } from "./content.constants";

const getImage = (images) => {
  if (images.length === 0) return ThumbnailImage;

  let baseUrl = LOCAL_BASE_URL || "http://localhost:1337";

  if (ENV === "prod") {
    baseUrl = PROD_BASE_URL;
  }

  if (ENV === "dev") {
    baseUrl = DEV_BASE_URL;
  }

  const url = `${baseUrl}${images[0].url}`;

  return url;
};

const Content = (props) => {
  const [state, setState] = useState({ isSaved: false, seeMoreOn: false });

  useEffect(() => {
    const { dispatch } = props;
    dispatch(checkArticleIsSaved(props, state, setState));
  }, []);

  const handleSaveForLater = () => {
    const { dispatch } = props;
    return dispatch(saveForLater(props, state, setState));
  };

  const handleUnsave = () => {
    const { dispatch } = props;
    return dispatch(unsaveArticle(props, state, setState));
  };

  const toggleSeeMore = () =>
    setState({ ...state, seeMoreOn: !state.seeMoreOn });

  const getDescription = (content) => {
    const { seeMoreOn } = state;
    if (!seeMoreOn) return `${content.substr(0, 120).trim()}...`;
    return content;
  };

  const renderRelevance = (relevance) => {
    if (!relevance || !RELEVANCE_LIST.includes(relevance)) return "N/A";
    if (relevance === "high") return <span className={styles.high}>High</span>;
    if (relevance === "low") return <span className={styles.low}>Low</span>;
    if (relevance === "medium")
      return <span className={styles.medium}>Medium</span>;
    return "N/A";
  };

  const renderSource = (source, link) => {
    if (!source || source === EMPTY_STRING) return "N/A";
    return (
      <a
        target="_blank"
        rel="noopener noreferrer"
        href={link}
        className={styles.sourceLink}
      >
        {source}
      </a>
    );
  };

  return (
    <div className={styles.articlecard}>
      <Row className="align-items-center">
        <Col xs={12}>
          <img
            className={cx("img-fluid", { [styles.imgfluid]: true })}
            src={getImage(props.images)}
            alt="img"
          />
          <div className={styles.articlecenter} href="#">
            <AlignCenter
              style={{ width: "16px", height: "16px", opacity: "0.5" }}
            />
            <span className={styles.art}>{props.resourceType}</span>
          </div>

          {state.isSaved ? (
            <button className={styles.book} onClick={handleUnsave}>
              <BsBookmarkFill style={{ fontSize: 21 }} />
            </button>
          ) : (
            <button className={styles.book} onClick={handleSaveForLater}>
              <Bookmark />
            </button>
          )}
        </Col>

        <Col xs={12}>
          <span className={styles.taxo}>{props.category}</span>
          <a
            className={styles.title}
            title={props.title}
            rel="noopener noreferrer"
            target={
              !props.externalLink || props.externalLink.length === 0
                ? null
                : "_blank"
            }
            href={
              !props.externalLink || props.externalLink.length === 0
                ? `/resource-article/${props.slug}`
                : props.externalLink
            }
          >
            {props.title}
            {/* {props.title.length > 33
              ? `${props.title.substr(0, 29)}...`
              : props.title} */}
          </a>
          <div className={styles.source}>By - {props.author}</div>
          <div className={styles.source}>
            Source: {renderSource(props.source, props.externalLink)}{" "}
          </div>
          <div className={styles.source}>
            Relevance: {renderRelevance(props.relevance)}&nbsp; &#x25cf; &nbsp;
            {props.publishedOn}
          </div>
          <p className={styles.excerpt}>
            {getDescription(props.excerpt)}
            <button className={styles.seeMoreBtn} onClick={toggleSeeMore}>
              {!state.seeMoreOn ? " See more" : " See less"}
            </button>
          </p>
        </Col>
      </Row>
    </div>
  );
};

export default Content;
