import React, { useState } from "react";
import cx from "classnames";
import Row from "react-bootstrap/Row";
import Container from "react-bootstrap/Container";
import Col from "react-bootstrap/Col";

import { EMPTY_STRING } from "constants/app.constants";

import styles from "./signupform.module.css";
import SignUpOtpForm from "../SignUpOtpForm";
import Action from "./signUpForm.actionHandlers";
import { DEFAULT_STATE, STATE_LIST } from "./signUpForm.constants";

const SignUpForm = (props) => {
  const [state, setState] = useState(DEFAULT_STATE);

  const handleChange = (event) => {
    return setState({
      ...state,
      [event.target.name]: event.target.value,
    });
  };

  const handleRadioChange = (event) => {
    return setState({
      ...state,
      medicalRegistrationNumber: EMPTY_STRING,
      stateName: EMPTY_STRING,
      registrationType: event.target.value,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    return Action.executeSignUp(state, setState);
  };

  if (state.otpSent) {
    return <SignUpOtpForm otpData={state.otpData} {...props} />;
  }

  return (
    <React.Fragment>
      <Row style={{ marginRight: 0, marginLeft: 0 }}>
        <Col md={12}>
          <h3 className={styles.pageTitle}>Sign Up</h3>
        </Col>
      </Row>
      <form onSubmit={handleSubmit}>
        <Row
          className="responsiveForm"
          style={{ marginLeft: 0, marginRight: 0 }}
        >
          <Col md={6} sm={12}>
            <label className={styles.label}>Full Name</label>
            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className={cx("input-group-text", { [styles.field]: true })}
                >
                  Dr.
                </span>
              </div>
              <input
                type="text"
                className="form-control"
                placeholder="Enter your full name"
                onChange={handleChange}
                value={state.fullName}
                name="fullName"
              />
            </div>
          </Col>

          <Col md={6} sm={12}>
            <label className={styles.label}>Speciality</label>
            <input
              type="text"
              className="form-control"
              placeholder="Enter speciality"
              onChange={handleChange}
              value={state.speciality}
              name="speciality"
            />
          </Col>

          <Col md={6} sm={12} style={{ marginTop: "13px" }}>
            <label className={styles.label}>Practice Location</label>
            <input
              type="text"
              className="form-control"
              placeholder="Enter practice location"
              onChange={handleChange}
              value={state.practiceLocation}
              name="practiceLocation"
            />
          </Col>

          <Col md={6} sm={12} style={{ marginTop: "13px" }}>
            <label className={styles.label}>Email Address</label>
            <input
              type="email"
              className="form-control"
              placeholder="Enter email address"
              onChange={handleChange}
              value={state.email}
              name="email"
            />
          </Col>
          <Col md={6} sm={12} style={{ marginTop: "13px" }}>
            <label className={styles.label}>Mobile Number</label>
            <div className="input-group mb-3">
              <div className="input-group-prepend">
                <span
                  className="input-group-text"
                  style={{ backgroundColor: "transparent" }}
                >
                  +91
                </span>
              </div>
              <input
                type="text"
                className="form-control"
                placeholder="Enter Mobile number"
                onChange={handleChange}
                value={state.phoneNumber}
                name="phoneNumber"
              />
            </div>
          </Col>
          <Col md={6} sm={12} style={{ marginTop: "13px" }}>
            <label className={styles.label}>Password</label>
            <div className="input-group mb-3">
              <input
                type="password"
                className="form-control"
                placeholder="Enter Password"
                onChange={handleChange}
                value={state.password}
                name="password"
              />
            </div>
          </Col>
          <Col md={6} sm={12} style={{ marginTop: "13px" }}>
            {" "}
          </Col>
          <Col
            md={12}
            sm={12}
            style={{ marginTop: "13px", marginBottom: "13px" }}
          >
            <label className={styles.label}>Registration Number</label>
            <br />
            <div className="form-check-inline">
              <label className="form-check-label">
                <input
                  type="radio"
                  className="form-check-input"
                  value="MRN"
                  checked={state.registrationType === "MRN"}
                  onChange={handleRadioChange}
                />
                Medical Registration number(MRN)
              </label>
            </div>
            <div className="form-check-inline">
              <label className="form-check-label">
                <input
                  type="radio"
                  className="form-check-input"
                  value="MCI"
                  checked={state.registrationType === "MCI"}
                  onChange={handleRadioChange}
                />
                Medical Council of India(MCI)
              </label>
            </div>
            <div
              className={cx("form-check-inline disabled", {
                [styles.selectBlock]: true,
              })}
            >
              <label className="form-check-label">
                <input
                  type="radio"
                  className="form-check-input"
                  value="OTHER"
                  checked={state.registrationType === "OTHER"}
                  onChange={handleRadioChange}
                />
                Others
              </label>
            </div>
          </Col>
          {state.registrationType === "MRN" && (
            <React.Fragment>
              <Col md={6} sm={12} style={{ marginBottom: 12 }}>
                <select
                  className="form-control"
                  value={state.stateName}
                  name="stateName"
                  onChange={handleChange}
                >
                  <option value="">Select State</option>
                  {STATE_LIST.map((stateName) => (
                    <option key={stateName} value={stateName}>
                      {stateName}
                    </option>
                  ))}
                </select>
              </Col>
              <Col md={6} sm={12}>
                <input
                  type="text"
                  className="form-control"
                  placeholder="Enter MRN"
                  onChange={handleChange}
                  value={state.medicalRegistrationNumber}
                  name="medicalRegistrationNumber"
                />
              </Col>
            </React.Fragment>
          )}

          {state.registrationType === "MCI" && (
            <Col md={12} sm={12}>
              <input
                type="text"
                className="form-control"
                placeholder="Enter MCI Registration Number"
                onChange={handleChange}
                value={state.medicalRegistrationNumber}
                name="medicalRegistrationNumber"
              />
            </Col>
          )}

          {state.registrationType === "OTHER" && (
            <Col md={12} sm={12}>
              <input
                type="text"
                className="form-control"
                placeholder="Enter Registration Number"
                onChange={handleChange}
                value={state.medicalRegistrationNumber}
                name="medicalRegistrationNumber"
              />
            </Col>
          )}
        </Row>
        <Container
          fluid
          style={{
            paddingTop: "20px",
            background: "#FFFFFF 0% 0% no-repeat padding-box",
            boxShadow: "0px -3px 20px #00000029",
            marginTop: "20px",
            position: "fixed",
            left: "0",
            bottom: "0",
            paddingBottom: "1rem",
          }}
        >
          <Row style={{ marginLeft: 0, marginRight: 0 }}>
            <Col md={12}>
              <center>
                {state.error ? (
                  <div className={styles.errorMessage}>
                    {state.errorMessage}
                  </div>
                ) : null}
                <button
                  className={cx("btn btn-primary sign_up_submit_btn", {
                    [styles.signUpBtn]: true,
                  })}
                  type="submit"
                  onClick={handleSubmit}
                  disabled={state.signUpInProgress}
                >
                  {" "}
                  SUBMIT{" "}
                </button>
                <p
                  style={{
                    fontSize: "12px",
                    fontWeight: "500",
                    textAlign: "center",
                    marginBottom: 4,
                  }}
                >
                  By clicking on submit I agree to all{" "}
                  <a href="/terms-and-conditions">Terms &amp; Conditions</a>
                </p>
                <p
                  style={{
                    fontSize: "14px",
                    marginTop: 0,
                    textAlign: "center",
                  }}
                >
                  Already have an account ? <a href="/sign-in">Sign In</a>
                </p>
              </center>
            </Col>
          </Row>
        </Container>
      </form>
    </React.Fragment>
  );
};
export default SignUpForm;
