import React from "react";
import { Helmet } from "react-helmet";

import Container from "components/bits/Container";
import Header from "components/utilities/Header";
import ForgotPasswordForm from "components/utilities/ForgotPasswordForm";
import { TITLE_PREFIX } from "constants/app.constants";

const ForgotPassword = (props) => {
  return (
    <div>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Forgot Password</title>
      </Helmet>

      <Container fluid style={{ padding: 0 }}>
        <Header {...props} />
        <ForgotPasswordForm />
      </Container>
    </div>
  );
};

export default ForgotPassword;
