import Auth from "services/apis/Auth";
import { REGEX, EMPTY_STRING } from "constants/app.constants";
import { _setErrorMsg } from "utils/common";

import {
  ERROR,
  MRN_TYPES,
  ACCEPTABLE_PWD_LENGTH,
} from "./signUpForm.constants";

export default {
  executeSignUp: async (state, setState) => {
    const {
      registrationType,
      medicalRegistrationNumber,
      email,
      fullName,
      phoneNumber,
      practiceLocation,
      speciality,
      stateName,
      password,
    } = state;

    if (
      medicalRegistrationNumber === EMPTY_STRING ||
      email === EMPTY_STRING ||
      fullName === EMPTY_STRING ||
      phoneNumber === EMPTY_STRING ||
      practiceLocation === EMPTY_STRING ||
      password === EMPTY_STRING
    ) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.EMPTY_FIELD,
      });
    }

    if (
      password.length < ACCEPTABLE_PWD_LENGTH[0] ||
      password.length > ACCEPTABLE_PWD_LENGTH[1]
    ) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.PWD_LENGTH,
      });
    }

    if (!REGEX.PASSWORD.test(password)) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.PWD_PATTERN,
      });
    }

    if (!MRN_TYPES.includes(registrationType)) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.INVALID_MRN,
      });
    }

    if (registrationType === "MRN" && stateName === EMPTY_STRING) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.EMPTY_STATE_NAME,
      });
    }

    if (!REGEX.EMAIL.test(email)) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.INVALID_EMAIL,
      });
    }

    const values = {
      registrationType,
      medicalRegistrationNumber,
      email,
      fullName,
      phoneNumber,
      practiceLocation,
      speciality,
      stateName,
      username: email.split("@")[0],
      password,
    };

    setState({ ...state, signUpInProgress: true });
    try {
      const response = await Auth.signUp(values);
      return setState({
        ...state,
        error: false,
        otpSent: true,
        otpData: response.data,
      });
    } catch (error) {
      // console.log("sign up error", error.response);
      return setState({
        ...state,
        error: true,
        signUpInProgress: false,
        errorMessage: _setErrorMsg(error.response.data, ERROR.SIGNUP_FAILED),
      });
    }
  },
};
