import produce from "immer";

import UICONTROL_ACTIONS from "constants/redux-actions/uiControl.actions";

const DEFAULT_STATE = {
  authCheckModalOpen: false,
  miniSearchBarOn: false,
};

export default (state = DEFAULT_STATE, action) =>
  produce(state, (draft) => {
    switch (action.type) {
      case UICONTROL_ACTIONS.OPEN_AUTHCHECK_MODAL:
        draft.authCheckModalOpen = true;
        break;

      case UICONTROL_ACTIONS.CLOSE_AUTHCHECK_MODAL:
        draft.authCheckModalOpen = false;
        break;

      case UICONTROL_ACTIONS.TOGGLE_MINI_SEARCHBAR:
        draft.miniSearchBarOn = !state.miniSearchBarOn;
        break;

      default:
    }
  });
