/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import cx from "classnames";
import { connect } from "react-redux";
import moment from "moment";
import { Share } from "react-feather";
import { FiCloudOff } from "react-icons/fi";
import { Bookmark, ArrowUp } from "react-feather";
import { Helmet } from "react-helmet";

import Container from "components/bits/Container";
import Row from "components/bits/Row";
import Col from "components/bits/Col";
import Navigation from "components/utilities/Navigation";
import Footer from "components/utilities/Footer";
import Spinner from "components/bits/Spinner";
import Content from "components/utilities/Content";
import ContentResource from "components/utilities/ContentResource";
import { TITLE_PREFIX } from "constants/app.constants";

import styles from "./resourceArticle.module.css";
import { getAuthInfo } from "./resourceArticle.selector";
import { fetchArticleDetails, vote } from "./resourceArticle.actionHandlers";
import { DEFAULT_STATE } from "./resourceArticle.constants";

const ResourceArticle = (props) => {
  const [state, setState] = useState(DEFAULT_STATE);

  useEffect(() => {
    const { dispatch, match } = props;
    dispatch(fetchArticleDetails(match.params.articleSlug, state, setState));
  }, []);

  const handleVote = () => {
    const { dispatch } = props;
    dispatch(vote(state.article.id, state, setState));
  };

  const renderContent = () => {
    if (state.loading) {
      return (
        <div style={{ width: "100%", textAlign: "center", paddingTop: 200 }}>
          <Spinner animation="border" variant="info" />
          <div style={{ marginTop: 20 }}>Loading...</div>
        </div>
      );
    }

    if (!state.article.id) {
      return (
        <Col xs={12} style={{ textAlign: "center", marginTop: 200 }}>
          <div>
            <FiCloudOff fontSize={46} />
          </div>
          <div style={{ fontSize: 24, marginTop: 16 }}>No content found</div>
        </Col>
      );
    }

    return (
      <Container style={{ marginTop: "90px" }}>
        <Row>
          <Col md={{ span: 8, offset: 2 }}>
            <Container className={styles.cont}>
              <Row>
                <ContentResource article={state.article} {...props} />
              </Row>
              <hr />
              <button
                className={cx("bookmark left", { [styles.bookmark]: true })}
              >
                <Share />
                <span>&nbsp;SHARE</span>
              </button>
              <button
                className={cx("bookmark left", { [styles.bookmark]: true })}
                style={{ marginLeft: 20 }}
              >
                <Bookmark />
                <span>SAVE FOR LATER</span>
              </button>
              <button
                className={cx("bookmark left", { [styles.bookmarkup]: true })}
                onClick={handleVote}
              >
                <ArrowUp />
                <span>&nbsp;&nbsp;Upvotes:{state.article.votes}</span>
              </button>
              <hr />
              {state.article.hashTags.split(" ").map((tag) => (
                <button
                  key={tag}
                  type="button"
                  className={cx("btn btn-light", {
                    [styles.buttonsgroup]: true,
                  })}
                >
                  {tag.substr(1, 20)}
                </button>
              ))}
              <hr />
              <div style={{ display: "inline-block" }}>
                <p className={styles.fontstylesmain}>ARTICLE BY</p>
                <p className={styles.fontstylesmiddle}>
                  {state.article.author}
                </p>
              </div>
              <div className={styles.curated}>
                <p className={styles.fontstylesmain}>CURATED BY</p>
                <p className={styles.fontstylesmiddle}>Dr Krishna Seshadhri</p>
              </div>
              <hr />
            </Container>
          </Col>

          <Col md={{ span: 8, offset: 2 }}>
            <Container className={styles.cont}>
              <div className={styles.sectiontitle}>
                <h2>Related Resources</h2>NavigationNew
              </div>
              <Row>
                {state.relatedArticles.map((article) => (
                  <Col xs={12} lg={4} key={article.id}>
                    <Content
                      id={article.id}
                      slug={article.slug}
                      resourceType={article.type}
                      category={article.category.name}
                      title={article.title}
                      author={article.author}
                      excerpt={`${article.description.slice(0, 140)}...`}
                      images={article.images}
                      publishedOn={moment(article.created_at).format(
                        "MMM DD, YYYY"
                      )}
                      externalLink={article.externalLink}
                      {...props}
                    />
                  </Col>
                ))}
              </Row>
            </Container>
          </Col>
        </Row>
      </Container>
    );
  };

  return (
    <>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Resource Details</title>
      </Helmet>
      <Navigation {...props} light />
      {renderContent()}
      <hr className={styles.horizontalLine} />
      <Footer />
    </>
  );
};

export default connect(getAuthInfo)(ResourceArticle);
