/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import { connect } from "react-redux";
import {
  Container,
  Row,
  Col,
  Form,
  InputGroup,
  FormControl,
  Button,
} from "react-bootstrap";
import { Helmet } from "react-helmet";
import { Editor } from "react-draft-wysiwyg";
import cx from "classnames";

import Navigation from "components/utilities/Navigation";
import Footer from "components/utilities/Footer";
import Spinner from "components/bits/Spinner";
import { TITLE_PREFIX } from "constants/app.constants";

import { fetchCategories, addArticle } from "./addArticle.actionHandlers";
import styles from "./addArticle.module.css";
import { getAuthInfo } from "./addArticle.selector";
import { DEFAULT_STATE, SUCCESS_MSG } from "./addArticle.constants";

const AddArticle = (props) => {
  const [state, setState] = useState(DEFAULT_STATE);

  useEffect(() => {
    const { dispatch } = props;
    dispatch(fetchCategories(state, setState));
  }, []);

  const handleChange = (e) => {
    return setState({
      ...state,
      [e.target.name]: e.target.value,
    });
  };

  const handleContentChange = (editorState) => {
    return setState({
      ...state,
      content: editorState,
    });
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    const { dispatch } = props;
    return dispatch(addArticle(state, setState));
  };

  const handleUploadImage = (e) => {
    return setState({ ...state, image: e.target.files[0] });
  };

  if (state.loading) {
    return (
      <div style={{ width: "100%", textAlign: "center", paddingTop: 200 }}>
        <Spinner animation="border" variant="info" />
        <div style={{ marginTop: 20 }}>Loading...</div>
      </div>
    );
  }

  const renderRequired = () => <span className={styles.required}>*</span>;

  return (
    <div>
      <Helmet>
        <meta name="description" content="Doctor Knowledge" />
        <title>{TITLE_PREFIX} | Home</title>
      </Helmet>

      <Navigation {...props} light />

      <Container className={styles.container}>
        <Row>
          <Col xs={12}>
            <h1 className={styles.pageTitle}>Add Article</h1>
          </Col>
          <Col xs={12}>
            <Form onSubmit={handleSubmit}>
              <Form.Group>
                <Form.Label>{renderRequired()} Select Category</Form.Label>
                <Form.Control
                  as="select"
                  name="category"
                  defaultValue={state.category}
                  onChange={handleChange}
                  custom
                >
                  <option value="">-- Select Category --</option>
                  {state.categories.map((category) => (
                    <option value={category.id} key={category.id}>
                      {category.name}
                    </option>
                  ))}
                </Form.Control>
              </Form.Group>

              <Form.Group>
                <Form.Label htmlFor="title" className={styles.label}>
                  {renderRequired()} Title
                </Form.Label>
                <InputGroup className="mb-2">
                  <FormControl
                    id="title"
                    value={state.title}
                    type="text"
                    placeholder="Title"
                    name="title"
                    onChange={handleChange}
                  />
                </InputGroup>
              </Form.Group>

              <Form.Group>
                <Form.Label htmlFor="content" className={styles.label}>
                  {renderRequired()} Content
                </Form.Label>
                <Editor
                  id="content"
                  editorState={state.content}
                  placeholder="Add some content..."
                  toolbarClassName={styles.toolbar}
                  wrapperClassName={styles.editorWrapper}
                  editorClassName={styles.editor}
                  onEditorStateChange={handleContentChange}
                  toolbar={{
                    options: ["inline"],
                  }}
                />
              </Form.Group>

              <Form.Group>
                <Form.Label htmlFor="image" className={styles.label}>
                  {renderRequired()} Upload Image
                </Form.Label>
                <Form.File.Input id="image" onChange={handleUploadImage} />
              </Form.Group>

              <Form.Group>
                <Form.Label htmlFor="externalLink" className={styles.label}>
                  External Link (This link will be opened in a new tab)
                </Form.Label>
                <InputGroup className="mb-2">
                  <FormControl
                    id="externalLink"
                    value={state.externalLink}
                    type="text"
                    placeholder="External link"
                    name="externalLink"
                    onChange={handleChange}
                  />
                </InputGroup>
              </Form.Group>

              <Form.Group>
                <Form.Label htmlFor="youtubeVideoLink" className={styles.label}>
                  Youtube Video Link (Insert video link from Youtube)
                </Form.Label>
                <InputGroup className="mb-2">
                  <FormControl
                    id="youtubeVideoLink"
                    value={state.youtubeVideoLink}
                    type="text"
                    placeholder="Youtube video link"
                    name="youtubeVideoLink"
                    onChange={handleChange}
                  />
                </InputGroup>
              </Form.Group>

              <Form.Group>
                <Form.Label
                  htmlFor="externalVideoLink"
                  className={styles.label}
                >
                  External Video Link (Insert video link from external sources)
                </Form.Label>
                <InputGroup className="mb-2">
                  <FormControl
                    id="externalVideoLink"
                    value={state.externalVideoLink}
                    type="text"
                    placeholder="External video link"
                    name="externalVideoLink"
                    onChange={handleChange}
                  />
                </InputGroup>
              </Form.Group>

              <Form.Group>
                <Form.Label htmlFor="hashTags" className={styles.label}>
                  Hash Tags
                </Form.Label>
                <InputGroup className="mb-2">
                  <FormControl
                    id="hashTags"
                    value={state.hashTags}
                    type="text"
                    placeholder="Add hashtags e.g #covid #prevention. Maximum six allowed"
                    name="hashTags"
                    onChange={handleChange}
                  />
                </InputGroup>
              </Form.Group>

              <Form.Group>
                {!state.error ? null : (
                  <div className={styles.errorMsg}>{state.errorMessage}</div>
                )}
                {!state.success ? null : (
                  <div className={styles.success}>{SUCCESS_MSG}</div>
                )}
              </Form.Group>

              <Form.Group>
                <Button
                  variant="primary"
                  type="submit"
                  disabled={state.loading || state.submitting}
                  className={cx("onboarding-btn", { [styles.submitBtn]: true })}
                  onClick={handleSubmit}
                >
                  Submit
                </Button>
              </Form.Group>
            </Form>
          </Col>
        </Row>
      </Container>
      <Footer />
    </div>
  );
};

export default connect(getAuthInfo)(AddArticle);
