import Auth from "services/apis/Auth";
import { REGEX, EMPTY_STRING } from "constants/app.constants";

import { ERROR } from "./resetPasswordForm.constants";

export default {
  resetPassword: async (otpData, state, setState) => {
    const { password, confirmPassword } = state;

    if (confirmPassword === EMPTY_STRING || password === EMPTY_STRING) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.EMPTY_FIELD,
      });
    }

    if (confirmPassword !== password) {
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.PWD_NOT_MATCH,
      });
    }

    if (!REGEX.PASSWORD.test(password))
      return setState({
        ...state,
        error: true,
        errorMessage: ERROR.PWD_PATTERN,
      });

    const values = {
      password,
      user: otpData.user,
    };

    setState({ ...state, resettingPassword: true });
    try {
      await Auth.resetPassword(values);
      return setState({
        ...state,
        error: false,
        resettingPassword: false,
        resetPasswordSuccess: true,
      });
    } catch (error) {
      // console.log("error", error);
      return setState({
        ...state,
        resettingPassword: false,
        error: true,
        errorMessage: ERROR.PASSWORD_RESET_FAILED,
      });
    }
  },
};
