/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import cx from "classnames";
import { Row, Col } from "react-bootstrap";
import { Bookmark } from "react-feather";

import ThumbnailImage from "assets/images/default-image.png";
import {
  ENV,
  DEV_BASE_URL,
  PROD_BASE_URL,
  LOCAL_BASE_URL,
  EMPTY_STRING,
} from "constants/app.constants";

import styles from "./trending.module.css";
import {
  goto,
  checkArticleIsSaved,
  saveForLater,
  unsaveArticle,
} from "./trending.actionHandlers";
import { RELEVANCE_LIST } from "./trending.constants";

const getImage = (images) => {
  if (images.length === 0) return ThumbnailImage;

  let baseUrl = LOCAL_BASE_URL || "http://localhost:1337";

  if (ENV === "prod") {
    baseUrl = PROD_BASE_URL;
  }

  if (ENV === "dev") {
    baseUrl = DEV_BASE_URL;
  }

  const url = `${baseUrl}${images[0].url}`;

  return url;
};

const Trending = (props) => {
  const [state, setState] = useState({ isSaved: false });

  useEffect(() => {
    const { dispatch } = props;
    dispatch(checkArticleIsSaved(props, state, setState));
  }, []);

  const handleGoto = (path) => {
    const { dispatch, history } = props;
    return dispatch(goto(path, history, props));
  };

  const handleSaveForLater = () => {
    const { dispatch } = props;
    return dispatch(saveForLater(props, state, setState));
  };

  const handleUnsave = () => {
    const { dispatch } = props;
    return dispatch(unsaveArticle(props, state, setState));
  };

  const renderRelevance = (relevance) => {
    if (!relevance || !RELEVANCE_LIST.includes(relevance)) return "N/A";
    if (relevance === "high") return <span className={styles.high}>High</span>;
    if (relevance === "low") return <span className={styles.low}>Low</span>;
    if (relevance === "medium")
      return <span className={styles.medium}>Medium</span>;
    return "N/A";
  };

  const renderSource = (source, link) => {
    if (!source || source === EMPTY_STRING) return "N/A";
    return (
      <a
        target="_blank"
        rel="noopener noreferrer"
        href={link}
        className={styles.sourceLink}
      >
        {source}
      </a>
    );
  };

  return (
    <div className={styles.articlecard}>
      <Row className="align-items-center">
        <Col xs={12} md={5}>
          <img
            className="img-fluid"
            src={getImage(props.images)}
            alt={props.title}
          />
        </Col>

        <Col xs={12} md={7}>
          <span className={styles.taxo}>{props.category}</span>
          <button
            className={styles.title}
            onClick={() => handleGoto(`/resource-article/${props.slug}`)}
            title={props.title}
          >
            {props.title}
          </button>

          <div className={styles.sourceRelevance}>
            <div className={styles.source}>By - {props.author}</div>
            <div className={styles.bull}>&bull;</div>
            <div className={styles.source}>
              Source: {renderSource(props.source, props.externalLink)}{" "}
            </div>
            <div className={styles.bull}>&bull;</div>
            <div className={styles.source}>
              Relevance: {renderRelevance(props.relevance)}
            </div>
          </div>

          <p
            onClick={() => handleGoto(`/resource-article/${props.slug}`)}
            className={styles.excerpt}
          >
            {props.excerpt}
          </p>
          <div className={styles.footer}>
            {state.isSaved ? (
              <button
                onClick={handleUnsave}
                className={cx("bookmark left", { [styles.bookmark]: true })}
              >
                <Bookmark />
                <span> SAVED</span>
              </button>
            ) : (
              <button
                onClick={handleSaveForLater}
                className={cx("bookmark left", { [styles.bookmark]: true })}
              >
                <Bookmark />
                <span>SAVE FOR LATER</span>
              </button>
            )}
            <p
              className={cx("published right", {
                [styles.publishedright]: true,
              })}
            >
              {props.publishedOn}
            </p>
          </div>
        </Col>
      </Row>
    </div>
  );
};

export default Trending;
