/* eslint-disable react-hooks/exhaustive-deps */
import React, { useState, useEffect } from "react";
import cx from "classnames";
import PropTypes from "prop-types";
import { Redirect } from "react-router-dom";
import { connect } from "react-redux";
import { Alert, Modal } from "react-bootstrap";

import styles from "./authCheckModal.module.css";
import { getInfo } from "./authCheckModal.selector";
import UICONTROL_ACTIONS from "constants/redux-actions/uiControl.actions";

const AuthCheckModal = (props) => {
  const [redirect, setRedirect] = useState(false);

  const closeModal = () => {
    const { dispatch } = props;
    return dispatch({ type: UICONTROL_ACTIONS.CLOSE_AUTHCHECK_MODAL });
  };

  const redirectTo = () => {
    setRedirect(true);
  };

  useEffect(() => {
    return () => {
      const { dispatch } = props;
      return dispatch({ type: UICONTROL_ACTIONS.CLOSE_AUTHCHECK_MODAL });
    };
  }, [redirect]);

  if (redirect) return <Redirect to="/sign-in" />;
  if (!props.authCheckModalOpen) return null;

  return (
    <Modal
      size="lg"
      aria-labelledby="contained-modal-title-vcenter"
      centered
      show={props.authCheckModalOpen}
      onHide={closeModal}
      className={styles.mymodal}
    >
      <Modal.Body>
        <div className={styles.modal}>
          <Alert variant="warning" className={styles.alert}>
            Kindly Sign in/ Sign up to view this page.
          </Alert>
        </div>
      </Modal.Body>
      <Modal.Footer className={styles.footer}>
        <div className={styles.btnContainer}>
          <div>
            {" "}
            <button
              type="button"
              className={cx("btn btn-link", { [styles.cancelBtn]: true })}
              onClick={closeModal}
            >
              <strong>Cancel</strong>
            </button>
            {/* <button  className={styles.cancelBtn} onClick={closeModal}>
              Cancel
            </button>*/}
            {/* <Button variant="link">Link</Button> */}
          </div>
          <div>
            <button className={styles.proceedBtn} onClick={redirectTo}>
              Sign In
            </button>
          </div>
        </div>
      </Modal.Footer>
    </Modal>
  );
};

AuthCheckModal.propTypes = {
  dispatch: PropTypes.func.isRequired,
  authCheckModalOpen: PropTypes.bool.isRequired,
};

export default connect(getInfo)(AuthCheckModal);
